import { analyticsActive, getUserInfo, type UserInfo } from '@particle-network/auth-core';
import { isClient } from '.';

export const sendAnalyticsActive = (
  action: 'sign' | 'login' | 'open' | 'open_wallet',
  userInfo?: Omit<UserInfo, 'token'>
) => {
  const info = userInfo ?? getUserInfo();
  if (info && isClient()) {
    let wallet = info.wallets.find((item) => item.chain_name !== 'solana');
    if (!wallet?.public_address) {
      wallet = info.wallets.find((item) => item.chain_name === 'solana');
    }
    if (wallet && wallet.public_address) {
      /* eslint-disable */
      analyticsActive({
        chain_id:
          wallet.chain_name !== 'solana'
            ? Number(window.particleAuth?.ethereum.chainId)
            : window.particleAuth?.solana.chainId || 101,
        identity: info.uuid,
        login_type: 'particle' as any,
        action: action as any,
        wallet_address: wallet.public_address,
        user_info: JSON.stringify(info),
      });
      /* eslint-enable */
    }
  }
};
