import { EvmRpcMethod, GasFeeMode, type EVMTransaction, type TokenPrice } from '@particle-network/auth-core';
import { useInterval } from 'ahooks';
import React, { useEffect, useState } from 'react';
import type { Chain as ViemChain } from 'viem/chains';
import SVGIcon from '../../../components/icon/svg-icon';
import { useParticleAuth, useTranslation } from '../../../context';
import { getNativeSymbol } from '../../../utils/chain-utils';
import { formatPrice } from '../../../utils/number-utils';
import { useEVMTransaction } from '../evmContextProvider';
import styles from './info-sign.less';
type IProps = {
  openGasDrawer: () => void;
  signLoading: boolean;
  chainInfo: ViemChain;
  signMethod: string;
};

function GasDisplay(props: IProps) {
  const { openGasDrawer, signLoading, chainInfo, signMethod } = props;

  const { t } = useTranslation();

  const { gasFeeResult, gasFeeMode, fetchGasAsync, gasFeeDisplay, tokenPrice, totalAmountDisplay, transactionData } =
    useEVMTransaction();

  const [interval, setInterval] = useState<number | undefined>(undefined);

  const [countdown, setCountdown] = useState(10);

  const { modalOptions } = useParticleAuth();

  useInterval(() => {
    const result = countdown - 1;
    if (result <= 0) {
      setCountdown(10);
      loadGasConfig();
    } else {
      setCountdown(result);
    }
  }, interval);

  useEffect(() => {
    if (signLoading) {
      setInterval(undefined);
      setCountdown(10);
    } else {
      setInterval(1000);
      loadGasConfig();
    }
    return () => {
      setInterval(undefined);
      setCountdown(10);
    };
  }, [signLoading]);

  const loadGasConfig = () => {
    if (transactionData) {
      fetchGasAsync({
        addresses: ['native'],
        from: transactionData.from,
        to: transactionData.to,
        value: transactionData.value,
        data: transactionData.data,
      });
    }
  };

  const editGas = () => {
    if (!gasFeeResult) {
      return;
    }
    openGasDrawer();
  };

  const estimatedTime = (data?: EVMTransaction): string => {
    if (!data || !gasFeeResult || !gasFeeMode) {
      return '';
    }
    let time: number;
    if (gasFeeMode == GasFeeMode.low) {
      time = gasFeeResult.low.maxWaitTime;
    } else if (gasFeeMode == GasFeeMode.medium) {
      time = gasFeeResult.medium.maxWaitTime;
    } else if (gasFeeMode == GasFeeMode.high) {
      time = gasFeeResult.high.maxWaitTime;
    } else {
      time = gasFeeResult.low.maxWaitTime;
    }
    let displayTime: string;
    if (time / 1000 >= 60) {
      displayTime = `>${time / 1000 / 60}min`;
    } else {
      displayTime = `<${time / 1000}s`;
    }

    return displayTime;
  };

  const formatDisplayPrice = (fee: string, prices: TokenPrice[]): string => {
    return prices.length > 0 && prices[0].currencies && prices[0].currencies.length > 0
      ? formatPrice({
          amount: fee,
          price: prices[0].currencies[0].price,
          fiatCoin: modalOptions.fiatCoin || 'USD',
        })
      : '';
  };

  return (
    <div>
      <style>{styles as unknown as string}</style>
      <div className='net-fee' onClick={editGas}>
        <div className='evm-fee'>
          <div className='fee-title'>
            {t('sign.evm_fee')}
            {gasFeeDisplay && interval && (
              <span>
                {' '}
                {t('sign.refresh_in')} {countdown}s
              </span>
            )}
          </div>

          {gasFeeDisplay && tokenPrice && (
            <div className='fee-row'>
              <div>
                {gasFeeDisplay.fee && (
                  <div>
                    {gasFeeDisplay.fee} {getNativeSymbol(chainInfo)}
                    <span className='approximately'> {formatDisplayPrice(gasFeeDisplay.fee, tokenPrice)}</span>
                  </div>
                )}
                {t('sign.max_fee')}
                {gasFeeDisplay.maxFee} {getNativeSymbol(chainInfo)}
                <span className='approximately'> {formatDisplayPrice(gasFeeDisplay.maxFee, tokenPrice)}</span>
              </div>
              <div className='fee-right'>
                <div>
                  {gasFeeDisplay && <div className='fee-standard'>{t(`sign.gas_${gasFeeMode}`)}</div>}
                  <div
                    className='fee-time'
                    style={estimatedTime(transactionData).search('min') !== -1 ? { color: '#F55F0D' } : {}}
                  >
                    {estimatedTime(transactionData)}
                  </div>
                </div>
                <div>
                  <SVGIcon className='arrow-right-icon' name='arrow_right_icon' />
                </div>
              </div>
            </div>
          )}
        </div>
      </div>

      {signMethod !== EvmRpcMethod.ethSendTransaction && (
        <div className='total'>
          <div className='total-title'>{t('sign.total')}</div>
          {totalAmountDisplay && tokenPrice && (
            <div className='total-content mt8'>
              {totalAmountDisplay.amount && (
                <div>
                  {totalAmountDisplay.amount} {getNativeSymbol(chainInfo)}
                  <span> {formatDisplayPrice(totalAmountDisplay.amount, tokenPrice)}</span>
                </div>
              )}
              <div>
                {t('sign.max_amount')}: {totalAmountDisplay.maxAmount} {getNativeSymbol(chainInfo)}
                <span> {formatDisplayPrice(totalAmountDisplay.maxAmount, tokenPrice)}</span>
              </div>
            </div>
          )}
        </div>
      )}
    </div>
  );
}

export default GasDisplay;
