import type { ReactNode } from 'react';
import React from 'react';
import { useCustomNavigate, useCustomRouter } from '../../context';
import SVGIcon from '../icon/svg-icon';
import styles from './index.less';
interface IProps {
  children?: ReactNode;
  leftContent?: ReactNode | string;
  rightContent?: ReactNode | string;
  displayBackBtn?: boolean;
  onBack?: any;
}

const Header = (props: IProps) => {
  const { children, leftContent, rightContent, onBack } = props;
  let { displayBackBtn = false } = props;

  const navigate = useCustomNavigate();
  const { customRouter } = useCustomRouter();
  if (onBack) {
    displayBackBtn = true;
  }

  if (leftContent || (customRouter?.history?.length ?? 0) <= 1) {
    displayBackBtn = false;
  }

  return (
    <div className='width-100'>
      <style>{styles as unknown as string}</style>
      <div className='p-auth-header'>
        <div className='left'>
          <>
            {displayBackBtn && (
              <SVGIcon
                className='header-back-btn'
                name='circle_back'
                onClick={() => {
                  if (onBack) {
                    onBack();
                  } else {
                    navigate(-1);
                  }
                }}
              />
            )}
            {leftContent}
          </>
        </div>
        <div className='middle'>{children}</div>
        <div className='right'>{rightContent}</div>
      </div>
    </div>
  );
};

export default Header;
