import base64url from 'base64url';
import qs from 'qs';
import { isServer } from '.';

export const isSocialAuthLogin = () => {
  const query = qs.parse(!isServer() ? window.location.search : '', {
    ignoreQueryPrefix: true,
  });
  const particleThirdpartyParamsContent = query?.particleThirdpartyParams as string;

  if (particleThirdpartyParamsContent) {
    const particleThirdpartyParams = JSON.parse(base64url.decode(particleThirdpartyParamsContent as string));
    const { appState, error } = particleThirdpartyParams;
    const appStateObj = appState ? JSON.parse(base64url.decode(appState as string)) : {};
    const { purpose } = appStateObj;
    return Boolean(!error) && purpose !== 'bindLoginAccount';
  }

  return false;
};
