import { Modal } from 'antd';
import React, { forwardRef, useEffect, useImperativeHandle, useState } from 'react';

import { useAuthCoreModal } from '../../context';
import ParticleDrawer from '../particle-drawer';
import styles from './index.less';

interface IProps {
  children?: React.ReactNode;
  visible?: boolean;
  containerSelector?: any;
  title?: string | any;
  modalProps?: any;
  locked?: 'modal' | 'drawer';
  drawerProps?: any;
  className?: string;
  closable?: boolean;
  onOpen?: () => void;
  onClose?: () => void;
}
const ParticleModal = forwardRef((props: IProps, ref) => {
  const { children, title, drawerProps = {}, modalProps = {} } = props;
  const { authCoreModal } = useAuthCoreModal();
  const [visible, setVisible] = useState(props.visible || false);

  const onClose = () => {
    setVisible(false);
    props.onClose && props.onClose();
  };

  const onOpen = () => {
    setVisible(true);
    props.onOpen && props.onOpen();
  };

  useImperativeHandle(ref, () => ({
    setVisible,
    onOpen,
    onClose,
  }));

  useEffect(() => {
    setVisible(props.visible ?? false);
  }, [props.visible]);

  if (modalProps?.style?.top) {
    modalProps.style.top = '';
  }

  return (
    <>
      <style>{styles as unknown as string}</style>
      {authCoreModal.viewType == 'desktop' || props.locked === 'modal' ? (
        <Modal
          title={title}
          open={visible}
          okText=''
          wrapClassName={`w-model-container ${props.className || ''}`}
          centered={true}
          maskClosable={false}
          getContainer={() => {
            return props.containerSelector
              ? document.querySelector(props.containerSelector)
              : authCoreModal.rootModalContainer || document.querySelector('body');
          }}
          onCancel={() => {
            onClose();
          }}
          closable={props.closable ?? true}
          // closeIcon={<MIcon name="icon_63" size={30} />}
          {...modalProps}
          styles={{
            ...modalProps.styles,
          }}
        >
          {children}
        </Modal>
      ) : (
        <ParticleDrawer
          className={`w-drawer-container ${props.className || ''}`}
          closable={props.closable ?? true}
          onClose={onClose}
          visible={visible}
          placement='bottom'
          getContainer={() => {
            return props.containerSelector
              ? document.querySelector(props.containerSelector)
              : authCoreModal.rootModalContainer || document.querySelector('body');
          }}
          extra={<>{/* <MIcon name="icon_63" size={30} onClick={() => onClose()} /> */}</>}
          title={title}
          {...drawerProps}
        >
          {children}
        </ParticleDrawer>
      )}
    </>
  );
});

export default ParticleModal;
