import { intToHex } from '@ethereumjs/util';
import {
  EvmEnhancedMethod,
  EvmRpcMethod,
  GasFeeMode,
  getEVMChainInfoById,
  type EVMTransaction,
  type GasFee,
  type GasFeeResult,
  type TokenPrice,
  type TotalAmount,
  type TransactionData,
  type TxData,
} from '@particle-network/auth-core';
import { useSetState } from 'ahooks';
import React, { createContext, useCallback, useContext, useEffect, useState } from 'react';
import type { Chain as ViemChain } from 'viem/chains';
import { mainnet } from 'viem/chains';
import { useModalOptions } from '../../context';
import { useEthereum } from '../../context/hooks';
import useMessage from '../../context/hooks/useMessage';
import { bnToHex } from '../../utils/common-utils';
import { ethereumUtils } from '../../utils/ethereumUtils';
import { toWei } from '../../utils/number-utils';
import { isEIP1559Type } from '../../utils/transaction-utils';

interface EVMState {
  setTransaction: (tx: EVMTransaction) => void;
  transactionData?: EVMTransaction;
  updateTransaction: (tx: Partial<TransactionData>) => void;
  gasFeeMode?: GasFeeMode;
  setGasFeeMode: (mode: GasFeeMode) => void;
  fetchGasAsync: (params: { addresses: string[]; from: string; to?: string; value?: string; data?: string }) => void;
  gasFeeDisplay?: GasFee;
  totalAmountDisplay?: TotalAmount;
  tokenPrice?: TokenPrice[];
  gasError?: { code: number; message: string };
  gasFeeResult?: GasFeeResult;
  latestGasLimit?: string;
  currentChain: ViemChain;
  fetchPendingTransactions: (account: string) => Promise<any[]>;
}

const defaultData: EVMState = {
  setTransaction: () => {},
  updateTransaction: () => {},
  setGasFeeMode: () => {},
  fetchGasAsync: () => {},
  currentChain: mainnet,
  fetchPendingTransactions: () => Promise.resolve([]),
};

export const EVMContext = createContext<EVMState>(defaultData);

export const EVMContextProvider = (props: { method: string; param: unknown; children: React.ReactNode }) => {
  const { modalOptions } = useModalOptions();
  const [evmTransaction, setEVMTransaction] = useSetState<EVMTransaction>({ from: '' });
  const [latestGasLimit, setLatestGasLimit] = useState<string>();
  const [tokenPrice, setTokenProce] = useState<TokenPrice[]>();
  const [feeMode, setFeeMode] = useState<GasFeeMode>(GasFeeMode.custom);
  const [gasError, setGasError] = useState<{ code: number; message: string }>();
  const [gasFeeResult, setGasFeeResult] = useState<GasFeeResult>();
  const [gasFeeDisplay, setGasFeeDisplay] = useState<GasFee>();
  const [totalAmountDisplay, setTotalAmountDisplay] = useState<TotalAmount>();
  const message = useMessage();
  const { chainInfo } = useEthereum();

  const getTxChain = () => {
    if (props.method === EvmRpcMethod.ethSendTransaction && (props.param as TxData).chainId) {
      const chainId = (props.param as TxData).chainId;
      const chain = getEVMChainInfoById(Number(chainId));
      return chain ?? chainInfo;
    } else {
      return chainInfo;
    }
  };
  //交易过程中如果切换了网络，不影响当前交易
  const [currentChain] = useState<ViemChain>(getTxChain());

  const updateTransaction = (data: Partial<TransactionData>) => {
    if (evmTransaction) {
      if (data.maxFeePerGas) {
        setEVMTransaction({ maxFeePerGas: data.maxFeePerGas });
      }

      if (data.maxPriorityFeePerGas) {
        setEVMTransaction({ maxPriorityFeePerGas: data.maxPriorityFeePerGas });
      }

      if (data.gasLimit) {
        setEVMTransaction({ gasLimit: data.gasLimit });
      }

      if (data.gasPrice) {
        setEVMTransaction({ gasPrice: data.gasPrice });
      }

      if (data.data) {
        setEVMTransaction({ data: data.data });
      }
    }
  };

  useEffect(() => {
    if (evmTransaction && gasFeeResult) {
      const params = {
        gasLimit: evmTransaction.gasLimit || '0x0',
        baseFee: bnToHex(toWei(gasFeeResult.baseFee, 'gwei')),
        maxFeePerGas: evmTransaction.maxFeePerGas,
        maxPriorityFeePerGas: evmTransaction.maxPriorityFeePerGas,
        gasPrice: evmTransaction.gasPrice,
      };
      const gasFeeDisplay = ethereumUtils.gasFee(params);
      setGasFeeDisplay(gasFeeDisplay);

      const totalAmountDisplay = ethereumUtils.totalAmount({
        value: evmTransaction.value,
        gasLimit: evmTransaction.gasLimit || '0x0',
        baseFee: bnToHex(toWei(gasFeeResult.baseFee, 'gwei')),
        maxFeePerGas: evmTransaction.maxFeePerGas,
        maxPriorityFeePerGas: evmTransaction.maxPriorityFeePerGas,
        gasPrice: evmTransaction.gasPrice,
      });
      setTotalAmountDisplay(totalAmountDisplay);
    }
  }, [evmTransaction, gasFeeResult]);

  const setTransaction = (transaction: EVMTransaction) => {
    setEVMTransaction(transaction);
    if (transaction.gasLevel) {
      setFeeMode(transaction.gasLevel as GasFeeMode);
      console.log('update gas fee mode', transaction.gasLevel);
    } else {
      setFeeMode(GasFeeMode.custom);
      console.log('update gas fee mode (default)', GasFeeMode.custom);
    }
  };

  const setGasFeeMode = (gasFeeMode: GasFeeMode) => {
    setFeeMode(gasFeeMode);
    console.log('update gas fee mode (setGasFeeMode)', gasFeeMode);
    if (evmTransaction && gasFeeResult && gasFeeMode !== GasFeeMode.custom) {
      if (isEIP1559Type(evmTransaction.type)) {
        setEVMTransaction({
          maxFeePerGas: bnToHex(toWei(gasFeeResult[gasFeeMode].maxFeePerGas, 'gwei')),
          maxPriorityFeePerGas: bnToHex(toWei(gasFeeResult[gasFeeMode].maxPriorityFeePerGas, 'gwei')),
        });
      } else {
        setEVMTransaction({
          gasPrice: bnToHex(toWei(gasFeeResult[gasFeeMode].maxFeePerGas, 'gwei')),
        });
      }
    }
  };

  const fetchGasAsync = (params: { addresses: string[]; from: string; to?: string; value?: string; data?: string }) => {
    fetchGas(params)
      .then(({ gasFee, prices, gasLimit }) => {
        setGasFeeResult(gasFee);
        setTokenProce(prices);
        let gasLimitValue: string;
        if (typeof gasLimit === 'string') {
          gasLimitValue = gasLimit;
          setLatestGasLimit(gasLimit);
          setGasError(undefined);
        } else {
          if (!latestGasLimit) {
            gasLimitValue = intToHex(50000);
            setLatestGasLimit(gasLimitValue);
            setGasError({
              message: gasLimit.message,
              code: -32000,
            });
          } else {
            gasLimitValue = latestGasLimit;
          }
        }

        if (evmTransaction) {
          if (isEIP1559Type(evmTransaction.type)) {
            //eip 1559
            if (feeMode && feeMode !== GasFeeMode.custom) {
              evmTransaction.maxFeePerGas = bnToHex(toWei(gasFee[feeMode].maxFeePerGas, 'gwei'));
              evmTransaction.maxPriorityFeePerGas = bnToHex(toWei(gasFee[feeMode].maxPriorityFeePerGas, 'gwei'));
            } else if (!evmTransaction.maxFeePerGas || !evmTransaction.maxPriorityFeePerGas) {
              evmTransaction.maxFeePerGas = bnToHex(toWei(gasFee.medium.maxFeePerGas, 'gwei'));
              evmTransaction.maxPriorityFeePerGas = bnToHex(toWei(gasFee.medium.maxPriorityFeePerGas, 'gwei'));
              if (!evmTransaction.gasLimit) {
                setFeeMode(GasFeeMode.medium);
                console.log('update gas fee mode (fulfilled)', GasFeeMode.medium);
              }
            }
          } else {
            if (feeMode && feeMode !== GasFeeMode.custom) {
              evmTransaction.gasPrice = bnToHex(toWei(gasFee[feeMode].maxFeePerGas, 'gwei'));
            } else if (!evmTransaction.gasPrice) {
              evmTransaction.gasPrice = bnToHex(toWei(gasFee.medium.maxFeePerGas, 'gwei'));
              if (!evmTransaction.gasLimit) {
                setFeeMode(GasFeeMode.medium);
                console.log('update gas fee mode (fulfilled)', GasFeeMode.medium);
              }
            }
          }

          if (
            !evmTransaction.gasLimit ||
            feeMode !== GasFeeMode.custom ||
            Number(evmTransaction.gasLimit) < Number(gasLimitValue)
          ) {
            evmTransaction.gasLimit = gasLimitValue;
            console.log('update gasLimit(state.value):', gasLimitValue);
          }
          setEVMTransaction(evmTransaction);
        }
      })
      .catch((error) => {
        if (error?.message) {
          message.error(error?.message.replace('code:-32000,', ''));
        }
      });
  };

  const fetchSuggestedGasFee = useCallback(async (): Promise<GasFeeResult> => {
    const result = await window.particleAuth?.ethereum.request({
      chainId: currentChain.id,
      method: EvmEnhancedMethod.suggestedGasFees,
    });
    return result;
  }, [currentChain]);

  const fetchPendingTransactions = useCallback(
    async (account: string): Promise<any[]> => {
      const result = await window.particleAuth?.ethereum.request({
        chainId: currentChain.id,
        method: EvmEnhancedMethod.getPendingTransactionsByAddress,
        params: [account],
      });
      return result;
    },
    [currentChain]
  );

  const fetchPrice = useCallback(
    async (addresses: string[]): Promise<TokenPrice[]> => {
      const result = await window.particleAuth?.ethereum.request({
        chainId: currentChain.id,
        method: EvmEnhancedMethod.getPrice,
        params: [addresses, [modalOptions.fiatCoin?.toLowerCase() || 'usd']],
      });
      return result;
    },
    [modalOptions.fiatCoin, currentChain]
  );

  const fetchEstimateGas = useCallback(
    async (param: { from: string; to?: string; value?: string; data?: string }): Promise<string | Error> => {
      if (!param.to || param.to.length === 0) {
        delete param.to;
      }
      try {
        const result = await window.particleAuth?.ethereum.request({
          chainId: currentChain.id,
          method: 'eth_estimateGas',
          params: [param],
        });
        return result;
      } catch (error: unknown) {
        return error as Error;
      }
    },
    [currentChain]
  );

  const fetchGas = useCallback(
    async ({
      addresses,
      from,
      to,
      value,
      data,
    }: {
      addresses: string[];
      from: string;
      to?: string;
      value?: string;
      data?: string;
    }) => {
      try {
        const result = await Promise.all([
          fetchSuggestedGasFee(),
          fetchPrice(addresses),
          fetchEstimateGas({
            from,
            to,
            value,
            data,
          }),
        ]);
        // The value we return becomes the `fulfilled` action payload
        return {
          gasFee: result[0],
          prices: result[1],
          gasLimit: result[2],
        };
      } catch (error: any) {
        if (error.code && error.message) {
          return Promise.reject({
            message: 'code:' + error.code + ', ' + error.message.toString(),
          });
        } else {
          return Promise.reject(error);
        }
      }
    },
    [fetchSuggestedGasFee, fetchPrice, fetchEstimateGas]
  );

  return (
    <EVMContext.Provider
      value={{
        setTransaction,
        updateTransaction,
        setGasFeeMode,
        fetchGasAsync,
        fetchPendingTransactions,
        transactionData: evmTransaction,
        gasFeeDisplay,
        totalAmountDisplay,
        tokenPrice,
        gasError,
        gasFeeMode: feeMode,
        gasFeeResult,
        latestGasLimit,
        currentChain,
      }}
    >
      {props.children}
    </EVMContext.Provider>
  );
};

export const useEVMTransaction = () => useContext(EVMContext);
