import type { SocialAuthType } from '@particle-network/auth-core';
import React from 'react';
import loading from '../../common/images/loading2.png';
import { useParticleAuth, useTranslation } from '../../context';
import useLogin from '../../context/hooks/useLogin';
import { getLoginLogos } from '../../utils';
import SVGIcon from '../icon/svg-icon';
import PowerFooter from '../power-footer';
import styles from './index.less';

const SocialLoading = ({ authType, isFailed = true }: { authType: string; isFailed: boolean }) => {
  const { t } = useTranslation();

  const [spin, setSpin] = React.useState(!isFailed);

  const { socialAuthLogin } = useLogin();

  const { modalOptions } = useParticleAuth();

  return (
    <div className='social-loading-content'>
      <style>{styles as unknown as string}</style>
      <div className='wrap'>
        <div
          className={`social-logo-content ${isFailed ? 'failed' : ''}`}
          onClick={() => {
            if (!isFailed || spin) return;

            setSpin(true);
            socialAuthLogin({
              socialType: authType as SocialAuthType,
            });
          }}
        >
          <div className='logo'>
            <img src={getLoginLogos(modalOptions.themeType)[authType] || ''} alt='logo' />
          </div>
          {spin && (
            <div className='spin'>
              <img src={loading} alt='loading' />
            </div>
          )}

          {isFailed && !spin && (
            <div className='refresh-btn'>
              <SVGIcon className='refresh-icon' name='refresh_icon' />
            </div>
          )}
        </div>
        <div className='title'>{!isFailed ? t('login.logging_you_in') : t('login.request_failed')}</div>
        {isFailed && <div className='desc'>{t('login.something_wrong')}</div>}
      </div>
      <PowerFooter className='footer-box-v2' />
    </div>
  );
};

export default SocialLoading;
