import { RecordType } from '@particle-network/analytics';
import {
  EvmEnhancedMethod,
  EvmRpcMethod,
  TransactionSmartType,
  analyticsRecord,
  defaultTokenIcon,
  getChainIcon,
  isConnected,
  syncUserInfo,
  type EVMDeserializeTransactionResult,
  type EVMFunction,
  type EVMNFTChange,
  type EVMTransaction,
} from '@particle-network/auth-core';
import { Button, Image, Modal, Tabs } from 'antd';
import { BigNumber } from 'bignumber.js';
import BN from 'bn.js';
//@ts-ignore
import jt from 'json-toy';
import React, { useEffect, useMemo, useRef, useState } from 'react';
import CopyToClipboard from 'react-copy-to-clipboard';
import { type EvmSignProps } from '..';
import { getEVMPublicAddress } from '../../../api/getPublicAddress';
import { AuthError } from '../../../api/model/authError';
import { PromptSettingType } from '../../../api/model/bundle';
import { AuthPage } from '../../../components/customRouter';
import CircleClose from '../../../components/icon/circle-close';
import IconCopy from '../../../components/icon/icon-copy';
import ParticleLoading from '../../../components/loading';
import PowerFooter from '../../../components/power-footer';
import { useAuthCoreModal, useCustomNavigate, useEvents, useParticleAuth, useTranslation } from '../../../context';
import { useEthereum } from '../../../context/hooks';
import { useError } from '../../../context/hooks/useError';
import useMessage from '../../../context/hooks/useMessage';
import { usePaymentPassword } from '../../../context/hooks/usePaymentPassword';
import usePending from '../../../context/hooks/usePending';
import { AuthCoreModalEvent } from '../../../context/types';
import { formatAddress, getChainDisplayName, getNativeSymbol } from '../../../utils/chain-utils';
import { shortString } from '../../../utils/common-utils';
import { ethereumUtils } from '../../../utils/ethereumUtils';
import { formatTokenAmount2, fromSunFormat, fromWeiFormat } from '../../../utils/number-utils';
import { isEIP1559Type, isEVMAddress, isTron, parseTransaction } from '../../../utils/transaction-utils';
import { useEVMTransaction } from '../evmContextProvider';
import riskModalStyle from '../riskModal/index.less';
import RiskReminder from '../riskReminder';
import RiskTypography from '../riskTypography';
import NewErcTransfers from './NewErcTransfers';
import EvmGas from './evm-gas';
import GasDisplay from './gas-display';
import styles from './info-sign.less';
import Menu from './menu';
import NoGas from './no-gas';

import biconomy000 from '../../../common/images/smartAccount/biconomy-000-icon.png';
import biconomyfff from '../../../common/images/smartAccount/biconomy-fff-icon.png';
import btc000 from '../../../common/images/smartAccount/btc-000-icon.png';
import btcfff from '../../../common/images/smartAccount/btc-fff-icon.png';
import cyberconnect000 from '../../../common/images/smartAccount/cyberconnect-000-icon.png';
import cyberconnectfff from '../../../common/images/smartAccount/cyberconnect-fff-icon.png';
import light000 from '../../../common/images/smartAccount/light-000-icon.png';
import lightfff from '../../../common/images/smartAccount/light-fff-icon.png';
import simple000 from '../../../common/images/smartAccount/simple-000-icon.png';
import simplefff from '../../../common/images/smartAccount/simple-fff-icon.png';
import universal000 from '../../../common/images/smartAccount/universal-000-icon.png';
import universalfff from '../../../common/images/smartAccount/universal-fff-icon.png';
import xterio from '../../../common/images/smartAccount/xterio-icon.png';
import { isTelegramWebApp } from '../../../utils/isTelegramWebApp';

enum RenderPageType {
  SIGN_MESSAGE,
  SEND_TX,
  SIGN_TYPE_DATA,
}

export const getAAIcon = (aaType: string, theme: string) => {
  aaType = (aaType ?? 'biconomy')?.toLowerCase();
  const aaIcons: Record<string, any> = {
    biconomyfff,
    biconomy000,
    cyberconnectfff,
    cyberconnect000,
    simplefff,
    simple000,
    lightfff,
    light000,
    btcfff,
    btc000,
    universal000,
    universalfff,
    xterio000: xterio,
    xteriofff: xterio,
  };
  const key = `${aaType}${theme === 'light' ? 'fff' : '000'}`;
  return aaIcons[key] ?? aaIcons['biconomyfff'];
};

function EvmSign(props: EvmSignProps) {
  const { method, param, loginAuthorizationSign } = props;

  const { t } = useTranslation();
  const message = useMessage();
  const [loading, setLoading] = useState(false);

  const [transactionInfo, setTransactionInfo] = useState<EVMDeserializeTransactionResult>();

  const infoSignRef = useRef(null);

  const [headerTitle, setHeaderTitle] = useState<string>('');
  const { authCoreModal } = useAuthCoreModal();
  const [headerDes, setHeaderDes] = useState<string>('');

  const [gasVis, setGasVis] = useState<boolean>(false);

  const navigate = useCustomNavigate();

  const [renderPageType, setRenderPageType] = useState<RenderPageType | ''>('');

  const [changeApproveAmount, setChangeApproveAmount] = useState<string>('');

  const { errorHandle } = useError();

  const { transactionData, gasError, setTransaction, updateTransaction, currentChain: chainInfo } = useEVMTransaction();

  const [riskPrompt, setRiskPrompt] = useState(false);

  const { events } = useEvents();

  const [addressDisplayed, setAddressDisplayed] = useState<string>();

  const { hasSetPaymentPassword, showSetPaymentPasswordOrConfirm } = usePaymentPassword();

  const { setPaymentVerify, setPaymentPassword, userInfo, modalOptions } = useParticleAuth();

  const { address } = useEthereum();

  const { checkPending } = usePending();

  const loadsecurityAccounts = () => {
    syncUserInfo()
      .then(() => {
        approveSign();
      })
      .catch((error) => {
        setLoading(false);
        message.error(error.message ?? 'Sign Error');
      });
  };

  const { TabPane } = Tabs;

  const isPersonalSign = useMemo(
    () => method === EvmRpcMethod.personalSign || method === EvmRpcMethod.personalSignUniq,
    [method]
  );

  const redirectToApp = ({ error, result }: { error?: AuthError; result?: unknown }) => {
    events.emit(AuthCoreModalEvent.SignResponse, {
      result,
      error,
    });
  };

  useEffect(() => {
    getEVMPublicAddress({
      erc4337: modalOptions.erc4337,
      chainId: chainInfo.id,
      method: method as EvmRpcMethod,
    })
      .then((addr) => {
        setAddressDisplayed(addr);
      })
      .catch((error) => {
        console.error('get public address error', error);
      });
  }, [chainInfo.id, modalOptions.erc4337, method]);

  useEffect(() => {
    let renderPageType: RenderPageType | '' = '';
    console.log(`evm sign method: ${method}, chainId: ${chainInfo.id}`);
    if (isPersonalSign) {
      //sign message
      setHeaderTitle(t('sign.signature_message') as string);
      setHeaderDes(t('sign.signature_title') as string);

      renderPageType = RenderPageType.SIGN_MESSAGE;
    } else if (method === EvmRpcMethod.ethSendTransaction) {
      //send tx
      setHeaderTitle(t('sign.send_transaction') as string);
      setHeaderDes(t('sign.approve_and').format(getChainDisplayName(chainInfo)));
      setTransactionData();
      renderPageType = RenderPageType.SEND_TX;
    } else if (method === EvmRpcMethod.ethSignTypedDataV4 || method === EvmRpcMethod.ethSignTypedDataV4Uniq) {
      deserializeTypedData(JSON.stringify(param)).then((res) => {
        if (res?.type === TransactionSmartType.SEAPORT_NFT_LISTING) {
          setHeaderTitle(t('sign.send_transaction') as string);
          setHeaderDes(t('sign.approve_and').format(getChainDisplayName(chainInfo)));
          renderPageType = RenderPageType.SEND_TX;
        } else {
          renderPageType = RenderPageType.SIGN_TYPE_DATA;
          setHeaderTitle(t('sign.sign_typed_data') as string);
          setHeaderDes(t('sign.signature_title') as string);
        }
        setRenderPageType(renderPageType);
      });
    } else if (method.includes(EvmRpcMethod.ethSignTypedData)) {
      //sign typed data
      setHeaderTitle(t('sign.sign_typed_data') as string);
      setHeaderDes(t('sign.signature_title') as string);
      renderPageType = RenderPageType.SIGN_TYPE_DATA;
    }

    setRenderPageType(renderPageType);
  }, [chainInfo, t]);

  const setTransactionData = () => {
    console.log('setTransactionData', param);
    const txData = parseTransaction(param as any, chainInfo);
    console.log('setTransactionData， after parse', txData);
    setTransaction(txData);
    deserializeTransaction(txData);
  };

  const deserializeTypedData = async (jsonStr: string) => {
    const result = await window.particleAuth?.ethereum
      .request({
        chainId: chainInfo.id,
        method: EvmEnhancedMethod.deserializeTypedData,
        params: [jsonStr],
      })
      .catch((error) => {
        console.log('deserializeTypedData error', error);
        message.error(error.message ?? 'deserializeTypedData Error');
      });
    setTransactionInfo(result);
    return result;
  };

  const deserializeTransaction = async (txData: EVMTransaction) => {
    if (!checkTxData(txData)) {
      return;
    }

    const module = await import('@ethereumjs/tx');
    const { TransactionFactory } = module.default || module;
    const tx = TransactionFactory.fromTxData(txData);
    window.particleAuth?.ethereum
      .request({
        chainId: chainInfo.id,
        method: EvmEnhancedMethod.deserializeTransaction,
        params: [address, addHexPrefix(tx.serialize().toString('hex'))],
      })
      .then((result) => {
        setTransactionInfo(result);
      })
      .catch((error) => {
        console.log('deserializeTransaction error', error);
        Modal.error({
          title: error.message ?? 'Deserialize Transaction Error',
          wrapClassName: 'auth-core-modal-error',
          getContainer: () => {
            return authCoreModal.rootBody as HTMLElement;
          },
          okCancel: true,
          cancelText: t('common.cancel'),
          okText: t('common.retry'),
          onOk: () => {
            deserializeTransaction(txData);
          },
        });
      });
  };

  const hasSecurityRisk = useMemo(() => {
    return transactionInfo?.securityDetection && transactionInfo?.securityDetection.length > 0;
  }, [transactionInfo?.securityDetection]);

  const checkTxData = (txData: EVMTransaction): boolean => {
    if (isTron(chainInfo)) {
      if (!txData.from || !txData.to || !txData.value) {
        Modal.error({
          title: 'Transaction error, see doc for more info',
          wrapClassName: 'auth-core-modal-error',
          getContainer: () => {
            return authCoreModal.rootBody as HTMLElement;
          },
          onOk: () => {
            redirectToApp({
              error: AuthError.paramsError(),
            });
          },
        });
        return false;
      }

      return true;
    }
    if (Number(txData.type) !== 0 && Number(txData.type) !== 1 && Number(txData.type) !== 2) {
      Modal.error({
        title: 'Transaction type error, see doc for more info',
        wrapClassName: 'auth-core-modal-error',
        getContainer: () => {
          return authCoreModal.rootBody as HTMLElement;
        },
        onOk: () => {
          redirectToApp({
            error: AuthError.paramsError(),
          });
        },
      });
      return false;
    } else if (isEIP1559Type(txData.type) && txData.maxFeePerGas && txData.maxPriorityFeePerGas) {
      const maxFeePerGasBN = new BN(stripHexPrefix(txData.maxFeePerGas), 16);
      const maxPriorityFeePerGasBN = new BN(stripHexPrefix(txData.maxPriorityFeePerGas), 16);
      if (maxFeePerGasBN.lte(maxPriorityFeePerGasBN)) {
        // maxFeePerGas cannot be less than maxPriorityFeePerGas
        Modal.error({
          title: 'maxFeePerGas cannot be less than maxPriorityFeePerGas.',
          wrapClassName: 'auth-core-modal-error',
          getContainer: () => {
            return authCoreModal.rootBody as HTMLElement;
          },
          onOk: () => {
            redirectToApp({
              error: AuthError.feeError(),
            });
          },
        });
        return false;
      }
    }
    return true;
  };

  const decodeMessage = (message: string): string => {
    const msg = Buffer.from(stripHexPrefix(message), 'hex').toString('utf-8');
    if (isPersonalSign) {
      if (/�/.test(msg)) {
        return message;
      }
    }
    return msg;
  };

  const formatFunction = (evmFunction: EVMFunction): string => {
    if (evmFunction.params.length > 0) {
      const p = evmFunction.params.map((item) => item.type).join(', ');
      return `${evmFunction.name}(${p})`;
    }
    return `${evmFunction.name}()`;
  };

  const approveSign = async (pendingConfirm = false) => {
    if (!isConnected()) {
      redirectToApp({
        error: AuthError.notLogin(),
      });
      return;
    }

    analyticsRecord({
      // eslint-disable-next-line camelcase
      record_type: RecordType.PAGE_SIGN_CONFIRM_BUTTON_CLICK, // confirm按钮点击
    });

    if (method === EvmRpcMethod.ethSendTransaction) {
      if (await checkPending(approveSign, pendingConfirm)) {
        return;
      } else if (userInfo?.security_account?.has_set_payment_password) {
        setPaymentVerify({
          visible: true,
          onVerifyCompleted: signTx,
        });
      } else {
        showSetPaymentPasswordOrConfirm(signTx);
      }
    } else if (method.includes(EvmRpcMethod.ethSignTypedData) || isPersonalSign) {
      if (userInfo?.security_account?.has_set_payment_password) {
        setPaymentVerify({
          visible: true,
          onVerifyCompleted: signData,
        });
      } else {
        if (
          method.includes(EvmRpcMethod.ethSignTypedData) ||
          modalOptions.promptSettingConfig?.promptPaymentPasswordSettingWhenSign === PromptSettingType.everyAndNotSkip
        ) {
          // method是eth_signTypedData 或者 强制设置支付密码
          showSetPaymentPasswordOrConfirm(signData);
        } else {
          signData();
        }
      }
    } else {
      Modal.error({
        title: `method ${method} not support`,
        wrapClassName: 'auth-core-modal-error',
        getContainer: () => {
          return authCoreModal.rootBody as HTMLElement;
        },
      });
    }
  };

  const signTx = async () => {
    if (!transactionData) return;

    setLoading(true);

    const unsigned = JSON.stringify(transactionData);
    console.log('unsigned tx:', unsigned);

    let result;
    try {
      result = await window.particleAuth?.ethereum.sendTransaction({ ...transactionData });
      analyticsRecord({
        // eslint-disable-next-line camelcase
        record_type: RecordType.PAGE_SIGN_CONFIRM_BUTTON_CLICK_SUCCESS, // confirm成功
      });
    } catch (error: any) {
      console.error(method, error);
      analyticsRecord({
        // eslint-disable-next-line camelcase
        record_type: RecordType.PAGE_SIGN_CONFIRM_BUTTON_CLICK_FAILURE, // confirm失败
      });
      if (error?.error_code === 50103 && !userInfo?.security_account?.has_set_payment_password) {
        loadsecurityAccounts();
      } else if (error?.message === 'Local Key not found' || error?.message === 'Master password decryption error') {
        navigate(AuthPage.MasterPasswordVerify);
      } else {
        Modal.error({
          title: error.message ?? 'Send Transaction Error',
          wrapClassName: 'auth-core-modal-error',
          getContainer: () => {
            return authCoreModal.rootBody as HTMLElement;
          },
          okText: t('common.confirm'),
          onOk: () => {
            redirectToApp({
              error: error,
            });
          },
        });
      }
    } finally {
      setLoading(false);
    }
    if (result) {
      redirectToApp({
        result,
      });
    }
  };

  const signData = async () => {
    setLoading(true);
    let result;
    try {
      if (isPersonalSign) {
        result = await window.particleAuth?.ethereum.signMessage(param as string);
      } else {
        result = await window.particleAuth?.ethereum.signTypedData({
          data: param as any,
          version: ethereumUtils.getSignTypedVersion(method),
          uniq: ethereumUtils.isSignTypedDataUniq(method),
        });
      }
      analyticsRecord({
        // eslint-disable-next-line camelcase
        record_type: RecordType.PAGE_SIGN_CONFIRM_BUTTON_CLICK_SUCCESS, // confirm成功
      });
    } catch (error: any) {
      console.error(method, error);
      analyticsRecord({
        // eslint-disable-next-line camelcase
        record_type: RecordType.PAGE_SIGN_CONFIRM_BUTTON_CLICK_FAILURE, // confirm失败
      });
      if (error?.error_code === 50103 && !userInfo?.security_account?.has_set_payment_password) {
        loadsecurityAccounts();
      } else if (error?.message === 'Local Key not found' || error?.message === 'Master password decryption error') {
        navigate(AuthPage.MasterPasswordVerify);
      } else {
        message.error(error.message ?? (isPersonalSign ? 'Sign Message Error' : 'Sign Typed Data Error'));
      }
    } finally {
      setLoading(false);
    }

    if (result) {
      if (loginAuthorizationSign) {
        events.emit(AuthCoreModalEvent.LoginSuccess, {
          ...userInfo,
          authorization: {
            message: param,
            signature: result,
          },
        });
      } else {
        redirectToApp({
          result,
        });
      }
    }
  };

  const cancelSign = async () => {
    if (loading) {
      return;
    }

    if (loginAuthorizationSign) {
      events.emit(AuthCoreModalEvent.LoginSuccess, userInfo);
    } else {
      //返回app
      redirectToApp({
        error: AuthError.userRejectedRequest(),
      });
    }
  };

  const formatValue = (data: any) => {
    if (isEVMAddress(data.value)) {
      return shortString(formatAddress(data.value, chainInfo));
    }
    return data.value;
  };

  const signMessageTitle = useMemo(() => {
    let title = '';
    if (method.includes(EvmRpcMethod.ethSignTypedData)) {
      try {
        const { primaryType } = param as any;
        title = primaryType;
      } catch (error) {
        // pase error
      }
    }
    return title || 'Message';
  }, [method, param]);

  const sMessage = useMemo(() => {
    if (method.includes(EvmRpcMethod.ethSignTypedData)) {
      try {
        const signQueryMessage = param as any;
        let { message } = signQueryMessage;

        if (!signQueryMessage.message) {
          message = signQueryMessage;
        }

        return message;
      } catch (error) {
        // pase error
      }
    }
    return {};
  }, [method, param]);

  const getRow = (key: string, index: number) => {
    key = key.replace('ROOT.', '');
    let value = jt.getValByKeyPath(sMessage, key);
    let isTitle = false;
    if (typeof value !== 'string' && typeof value !== 'number') {
      value = '';
      isTitle = true;
    }
    const indent = key.split('.').length;

    return (
      <div key={index} className='s-row'>
        <div
          className='label'
          style={{
            paddingLeft: 20 * indent,
          }}
          data-type={isTitle ? 'title' : ''}
        >
          {key.split('.').pop()}：
        </div>

        {isEVMAddress(value) ? (
          <CopyToClipboard text={value} onCopy={() => message.success(t('new.copied_to'))}>
            <div className='value copy-text'>{formatValue({ value })}</div>
          </CopyToClipboard>
        ) : (
          <div
            className='value'
            onClick={(e) => {
              e.stopPropagation();
            }}
          >
            {value}
          </div>
        )}
      </div>
    );
  };
  // sign message
  const signMessage = () => {
    return (
      <div className='sign-message'>
        <div
          className={'message' + (hasSetPaymentPassword ? '' : ' no-password-tip')}
          data-transaction-type={transactionInfo?.type}
        >
          {!method.includes(EvmRpcMethod.ethSignTypedData) && (
            <div className='pre-wrap personal-message'>{decodeMessage(param as string)}</div>
          )}

          {method.includes(EvmRpcMethod.ethSignTypedData) && (
            <>
              <div className='s-row'>
                <div className='label' data-type='title'>
                  {signMessageTitle}
                </div>
              </div>
              {jt.travelJson(sMessage).map((key: string, index: number) => {
                return getRow(key, index);
              })}
            </>
          )}
        </div>
      </div>
    );
  };

  const getNFTName = (info: EVMNFTChange): string => {
    if (info.name && info.name.length > 0) {
      return info.name;
    }

    return `NFT#${info.tokenId}`;
  };

  const approveDisabled = (data?: EVMTransaction): boolean => {
    if (method === EvmRpcMethod.ethSendTransaction) {
      if (!data) {
        return true;
      }

      if (isTron(chainInfo)) {
        return false;
      }

      if (isEIP1559Type(data.type)) {
        return !data.gasLimit || !data.maxPriorityFeePerGas || !data.maxFeePerGas;
      } else {
        return !data.gasLimit || !data.gasPrice;
      }
    }
    return false;
  };
  const [displayDetail, setDisplayDetail] = useState<boolean>(false);

  const isErc4361 = useMemo(() => {
    // https://eips.ethereum.org/EIPS/eip-4361#example-message
    let result = false;
    if (isPersonalSign) {
      const signMessage = decodeMessage(param as string);
      const domain = signMessage.match(/^(.+)?\swants you/)?.[1];
      const address = signMessage.match(/wants you to sign in with your Ethereum account:\n(.*)/)?.[1];
      const uri = signMessage.match(/URI:(.*)/)?.[1];
      const version = signMessage.match(/Version:(.*)/)?.[1];
      const chainId = signMessage.match(/Chain ID:(.*)/)?.[1];
      const nonce = signMessage.match(/Nonce:(.*)/)?.[1];
      if (domain && address && uri && version && chainId && nonce) {
        result = true;
      }
    }
    return result;
  }, [transactionData, isPersonalSign]);

  useEffect(() => {
    if (isErc4361) {
      setHeaderTitle(t('new.sign_in_request') as string);
      setHeaderDes(t('new.requesting_sign_4361') as string);
    }
  }, [isErc4361]);

  // sign/send transaction
  const signTransaction = () => {
    return (
      <Tabs defaultActiveKey='1'>
        <TabPane tab={t('sign.details')} key='1'>
          <div className='balance-change'>
            <div className='title'>{t('sign.estimated_balance_change')}</div>
            <div className='change-body'>
              {transactionInfo?.estimatedChanges?.natives
                ?.filter((info) => info.address.toLowerCase() === address?.toLowerCase())
                ?.map((info, index) => {
                  return (
                    <div className='change-title' key={`native-change-${index}`}>
                      {getNativeSymbol(chainInfo)}
                      <div className='change-val' style={info.nativeChange.includes('-') ? { color: '#ea4335' } : {}}>
                        {info.nativeChange.includes('-') || info.nativeChange === '0' ? '' : '+'}
                        {isTron(chainInfo)
                          ? fromSunFormat(info.nativeChange)
                          : fromWeiFormat(info.nativeChange, 'ether', 18)}
                      </div>
                    </div>
                  );
                })}

              {transactionInfo?.estimatedChanges?.nfts?.map((info, index) => {
                return (
                  <div className='change-title' key={`nft-change-${index}`}>
                    {getNFTName(info)}
                    <div className='change-val' style={info.amountChange < 0 ? { color: '#ea4335' } : {}}>
                      {info.amountChange < 0 ? '' : '+'}
                      {info.amountChange}
                    </div>
                  </div>
                );
              })}

              {transactionInfo?.estimatedChanges?.tokens?.map((info, index) => {
                return (
                  <div className='change-title' key={`token-change-${index}`}>
                    {info.name ? info.name : 'Unknown Token'}
                    <div className='change-val' style={info.amountChange < 0 ? { color: '#ea4335' } : {}}>
                      {info.amountChange < 0 ? '' : '+'}
                      {formatTokenAmount2(info.amountChange, info.decimals)}
                    </div>
                  </div>
                );
              })}
            </div>
          </div>

          {transactionInfo && (
            <div className='from-to'>
              <div className='address-item'>
                <div>{t('sign.from')}</div>
                <div>{shortString(formatAddress(transactionInfo.data.from, chainInfo))}</div>
              </div>
              <div className='address-item mt10'>
                <div>{t('sign.to')}</div>
                <div>{shortString(formatAddress(transactionInfo.data.to, chainInfo))}</div>
              </div>
              {!isTron(chainInfo) && (
                <div className='address-item mt10'>
                  <div>{t('sign.nonce')}</div>
                  <div>#{parseInt(transactionInfo.data.nonce)}</div>
                </div>
              )}
            </div>
          )}

          {!gasError && transactionData && !isTron(chainInfo) && (
            <GasDisplay
              openGasDrawer={() => setGasVis(true)}
              signLoading={loading}
              chainInfo={chainInfo}
              signMethod={method}
            />
          )}

          {gasError && !isTron(chainInfo) && <NoGas />}
        </TabPane>
        <TabPane tab={t('sign.data')} key='2'>
          <div>
            {transactionInfo && transactionInfo.data.function && (
              <div className='inner-instruction' key={'instruction-function'}>
                <div className='inner-content'>
                  <div className='content-item'>
                    <div className='item'>
                      <div className='item-0'>
                        {t('sign.function_type')} {formatFunction(transactionInfo.data.function)}
                      </div>

                      {transactionInfo.data.function.params.map((item) => (
                        <div className='item-1 mt10' key={`instruction-function${item.name}`}>
                          {shortString(item.name)}
                          <span>{shortString(item.value)}</span>
                        </div>
                      ))}
                    </div>
                  </div>
                </div>
              </div>
            )}

            {/* hex data */}

            {transactionInfo && (
              <div className='inner-instruction' key={'instruction-hex-data'}>
                <div className='inner-content'>
                  <div className='content-item'>
                    <div className='item'>
                      <div className='item-0'>{t('sign.hex_data')}</div>
                      <div className='item-1 mt10'>
                        <div className='data'>{transactionInfo.data.data}</div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            )}
          </div>
        </TabPane>
      </Tabs>
    );
  };

  const editApproveAmount = async (amount: string) => {
    setChangeApproveAmount(amount);

    if (amount && amount !== '' && transactionInfo) {
      const { decimals = 18 } = transactionInfo?.estimatedChanges?.tokens?.[0] || {};

      BigNumber.config({ EXPONENTIAL_AT: [-256, 256] });
      const bn = new BigNumber(amount).multipliedBy(new BigNumber(10).pow(decimals));
      const approveAmount = bn.toString();
      BigNumber.config({ EXPONENTIAL_AT: [-7, 21] });

      const spender = transactionInfo.data?.function?.params?.[0]?.value || '';

      const encodeData = await window.particleAuth?.ethereum.request({
        chainId: chainInfo.id,
        method: 'particle_abi_encodeFunctionCall',
        params: [transactionInfo.data.to, 'erc20_approve', [spender, approveAmount]],
      });

      updateTransaction({
        data: encodeData,
      });
    }
  };

  return (
    <>
      <style>{styles as unknown as string}</style>
      {transactionInfo?.type ||
      renderPageType === RenderPageType.SIGN_TYPE_DATA ||
      renderPageType === RenderPageType.SIGN_MESSAGE ? (
        <div
          className={`info-sign info-sign-${transactionInfo?.type}`}
          data-type={transactionInfo?.type}
          ref={infoSignRef}
        >
          {!hasSetPaymentPassword && (
            <div className='has-payment-password' data-telegram={isTelegramWebApp()}>
              <div className='has-payment-password-icon'></div>
              <div className='has-payment-password-tip'>{t('account.waring_tip1')}</div>
              <div className='has-payment-password-set' onClick={setPaymentPassword}>
                {t('account.set')}
              </div>
            </div>
          )}
          <div className={'scroll-part' + (hasSetPaymentPassword ? '' : ' no-password-tip')}>
            <Menu userInfo={userInfo} transactionInfo={transactionInfo} />
            <div className='info-request'>
              {modalOptions.erc4337 && method !== EvmRpcMethod.ethSendTransaction && (
                // <div className="aa-tag">AA</div>
                <div className='aa-icon'>
                  <Image
                    src={getAAIcon(
                      ((modalOptions.erc4337 as any)?.name ?? 'BICONOMY').toLowerCase(),
                      modalOptions.themeType || 'light'
                    )}
                    fallback={defaultTokenIcon}
                    preview={false}
                  />
                </div>
              )}
              {headerTitle}
            </div>
            <div className='info-title'>
              <Image src={getChainIcon(chainInfo)} fallback={defaultTokenIcon} preview={false} />
              {getChainDisplayName(chainInfo)}
            </div>
            <CopyToClipboard text={addressDisplayed || ''} onCopy={() => message.success(t('new.copied_to'))}>
              <div className='info-address'>
                {shortString(addressDisplayed)}
                <div className='copy-icon'>
                  <IconCopy />
                </div>
              </div>
            </CopyToClipboard>
            <div className='info-des'>{headerDes}</div>
            <div className='apart-line'></div>
            {transactionInfo?.type === TransactionSmartType.NativeTransfer ||
            transactionInfo?.type === TransactionSmartType.ERC20_TRANSFER ||
            transactionInfo?.type === TransactionSmartType.ERC20_APPROVE ||
            transactionInfo?.type === TransactionSmartType.ERC721_TRANFER ||
            transactionInfo?.type === TransactionSmartType.ERC1155_TRANFER ||
            transactionInfo?.type === TransactionSmartType.SEAPORT_CANCEL_ORDER ||
            transactionInfo?.type === TransactionSmartType.SEAPORT_FULFILL_ORDER ||
            transactionInfo?.type === TransactionSmartType.SEAPORT_NFT_LISTING ? (
              <NewErcTransfers
                setDisplayDetail={setDisplayDetail}
                displayDetail={displayDetail}
                gasError={gasError}
                setGasVis={setGasVis}
                formatFunction={formatFunction}
                transactionInfo={transactionInfo}
                changeApproveAmount={changeApproveAmount}
                setChangeApproveAmount={editApproveAmount}
                signLoading={loading}
                signMessage={signMessage}
                chainInfo={chainInfo}
                signMethod={method}
              />
            ) : method === EvmRpcMethod.ethSendTransaction ? (
              signTransaction()
            ) : method !== EvmRpcMethod.ethSendTransaction ? (
              signMessage()
            ) : (
              ''
            )}
          </div>

          <div className='btn-box'>
            <div>
              <Button className='btn-cancel' onClick={cancelSign}>
                {t('common.cancel')}
              </Button>
              <Button
                className={`btn-approve ${gasError || hasSecurityRisk ? 'still-confirm' : ''}`}
                onClick={() => {
                  if (hasSecurityRisk) {
                    setRiskPrompt(true);
                  } else {
                    approveSign();
                  }
                }}
                loading={loading}
                disabled={approveDisabled(transactionData)}
              >
                {isErc4361
                  ? t('new.sign_in')
                  : gasError || hasSecurityRisk
                  ? t('common.still_confirm')
                  : t('common.confirm')}
              </Button>
            </div>

            <PowerFooter></PowerFooter>
          </div>

          {hasSecurityRisk && transactionInfo?.securityDetection && (
            <>
              <RiskReminder securityDetection={transactionInfo?.securityDetection}></RiskReminder>
              <Modal
                className='risk-modal'
                open={riskPrompt}
                closeIcon={<CircleClose />}
                centered
                maskClosable={false}
                onCancel={() => {
                  setRiskPrompt(false);
                }}
                getContainer={() => {
                  return authCoreModal.rootBody as HTMLDivElement;
                }}
              >
                <style>{riskModalStyle as unknown as string}</style>
                <div className='risk-modal-title'>{t('sign.risk_hint_title')}</div>
                <RiskTypography
                  className='risk-modal-content'
                  securityDetection={transactionInfo?.securityDetection}
                  title={t('sign.risk_identified') as string}
                />
                <div className='risk-bottom-btn-box'>
                  <Button
                    className='primary-antd-btn secondary'
                    onClick={() => {
                      setRiskPrompt(false);
                    }}
                  >
                    {t('common.cancel')}
                  </Button>
                  <Button
                    className='primary-antd-btn danger'
                    onClick={() => {
                      approveSign();
                      setRiskPrompt(false);
                    }}
                  >
                    {t('common.confirm')}
                  </Button>
                </div>
              </Modal>
            </>
          )}

          <EvmGas openGasDrawer={() => setGasVis(false)} visible={gasVis} />
        </div>
      ) : (
        <ParticleLoading />
      )}
    </>
  );
}

export default EvmSign;
