import { RecordType } from '@particle-network/analytics';
import { analyticsRecord, restoreWallet } from '@particle-network/auth-core';
import { useRequest } from 'ahooks';
import { Button, Form, Space, Typography } from 'antd';
import React, { useEffect, useState } from 'react';
import InputPassword from '../../../components/input-password';
import PowerFooter from '../../../components/power-footer';
import { useCustomNavigate, useParticleAuth, useTranslation } from '../../../context';
import useMessage from '../../../context/hooks/useMessage';
import styles from './index.less';

export type MasterPasswordVerifyProps = {
  loginVerifyMasterPassword?: boolean;
};

const MasterPasswordVerify = (props?: MasterPasswordVerifyProps) => {
  const { Text, Link } = Typography;
  const navigate = useCustomNavigate();
  const [showPasswordError, setShowPasswordError] = useState(false);
  const { t } = useTranslation();

  const { loginVerifyMasterPassword } = props || {};
  const containerRef = React.useRef<any>(null);
  const [nextDisabled, setNextDisabled] = useState(true);
  const { loginSuccessRedirectToApp } = useParticleAuth();
  const message = useMessage();
  const { run: runCheckMasterPassword, loading } = useRequest(restoreWallet, {
    manual: true,
    onBefore: () => {
      analyticsRecord({
        // eslint-disable-next-line camelcase
        record_type: RecordType.PAGE_MASTER_PASSWORD_VERIFY, // 主密码验证
      });
    },
    onSuccess: (result: boolean) => {
      if (result) {
        analyticsRecord({
          // eslint-disable-next-line camelcase
          record_type: RecordType.PAGE_MASTER_PASSWORD_VERIFY_SUCCESS, // 主密码验证成功
        });
        if (loginVerifyMasterPassword) {
          loginSuccessRedirectToApp();
        } else {
          navigate(-1);
        }
      } else {
        setShowPasswordError(true);
      }
    },
    onError: (error) => {
      console.log('checkMasterPassword error', error);
      message.error(error?.message || 'check master password error');
    },
  });

  const toDecription = () => {
    navigate('/account/master-password/description');
  };

  const verifyMasterPassword = (values: any) => {
    const { password } = values;
    if (password?.length >= 6 && password?.length <= 20) {
      setShowPasswordError(false);
      runCheckMasterPassword(password);
    } else {
      setShowPasswordError(true);
    }
  };

  const onValuesChange = (values: any) => {
    const { password } = values;
    setShowPasswordError(false);
    setNextDisabled(!password);
  };

  useEffect(() => {
    setTimeout(() => {
      containerRef.current?.querySelector('.password-input input')?.focus();
    }, 200);
  }, []);

  return (
    <div className='mp-verify-container' ref={containerRef}>
      <style>{styles as unknown as string}</style>
      <div className='mp-verify-title'>{t('account.restore_wallet')}</div>
      <Form layout='vertical' onFinish={verifyMasterPassword} onValuesChange={onValuesChange}>
        <div className='scroll-content'>
          <div className='mp-input-name padding-top-16'>{t('account.master_password')}</div>
          <Form.Item name='password'>
            <InputPassword className='mp-input' />
          </Form.Item>
          {showPasswordError && <div className='mp-input-error'>{t('account.password_error')}</div>}
          <Space direction='vertical' className='mp-tip-space'>
            <Text>{t('account.mpc_tss_intro')}</Text>
            <Text>
              {t('account.input_decrypt_hint')}{' '}
              <Link onClick={toDecription} className='more-text-btn'>
                {t('account.learn_more_period')}
              </Link>
            </Text>
          </Space>
        </div>
        <Form.Item>
          <div className='bottom-container footer'>
            <Button className='primary-antd-btn mp-next' htmlType='submit' loading={loading} disabled={nextDisabled}>
              {t('common.confirm')}
            </Button>
            <PowerFooter className='footer-box-v2' />
          </div>
        </Form.Item>
      </Form>
    </div>
  );
};

export default MasterPasswordVerify;
