import type { AuthType, ERC4337Options } from '@particle-network/auth-core';
import type { WalletOption } from '@particle-network/wallet';
import { useParticleAuth } from '..';
import type { CurrencyUnit, Theme } from '../../types';
import { type CustomStyle, type Language } from '../types';
import type { PromptSettingConfig } from './../../api/model/bundle';

export const useCustomize = () => {
  const { modalOptions, setModalOptions } = useParticleAuth();

  const setThemeType = (themeType?: Theme) => {
    setModalOptions({
      themeType,
    });
  };

  const setCustomStyle = (customStyle?: CustomStyle) => {
    setModalOptions({
      customStyle: customStyle ? { ...customStyle } : undefined,
    });
  };

  const setLanguage = (language?: Language) => {
    setModalOptions({
      language,
    });
  };

  const setAuthTypes = (authTypes?: AuthType[]) => {
    setModalOptions({
      authTypes: authTypes ? [...authTypes] : undefined,
    });
  };

  const setFiatCoin = (fiatCoin?: CurrencyUnit) => {
    setModalOptions({
      fiatCoin,
    });
  };

  const setERC4337 = (erc4337?: ERC4337Options) => {
    setModalOptions({
      erc4337,
    });
  };

  const setWalletOptions = (wallet: Omit<WalletOption, 'language' | 'erc4337'> | false) => {
    setModalOptions({
      wallet: wallet === false ? wallet : { ...wallet },
    });
  };

  const setPromptSettingConfig = (promptSettingConfig?: PromptSettingConfig) => {
    setModalOptions({
      promptSettingConfig,
    });
  };

  return {
    themeType: modalOptions.themeType,
    setThemeType,
    customStyle: modalOptions.customStyle,
    setCustomStyle: setCustomStyle,
    walletOptions: modalOptions.wallet,
    setWalletOptions,
    language: modalOptions.language,
    setLanguage,
    authTypes: modalOptions.authTypes,
    setAuthTypes,
    fiatCoin: modalOptions.fiatCoin,
    setFiatCoin,
    erc4337: modalOptions.erc4337,
    setERC4337,
    promptSettingConfig: modalOptions.promptSettingConfig,
    setPromptSettingConfig,
  };
};
