import type { ConnectParam, RpcInput } from '@particle-network/auth-core';
import type { PublicKey, Transaction, VersionedTransaction } from '@solana/web3.js';

export class SolanaWallet {
  readonly isParticleNetwork = true;

  isSocialConnecting?: boolean;

  name = 'Particle';
  url = 'https://particle.network';
  icon = 'https://static.particle.network/wallet-icons/Particle.png';

  on(event: string, listener: (...args: any[]) => void): this {
    window.particleAuth?.solana.on(event, listener);
    return this;
  }

  once(event: string, listener: (...args: any[]) => void): this {
    window.particleAuth?.solana.once(event, listener);
    return this;
  }

  off(event: string, listener: (...args: any[]) => void): this {
    window.particleAuth?.solana.off(event, listener);
    return this;
  }

  removeListener(event: string, listener: (...args: any[]) => void): this {
    window.particleAuth?.solana.removeListener(event, listener);
    return this;
  }

  get isConnected(): boolean {
    return window.particleAuth?.solana.isConnected ?? false;
  }

  get connecting(): boolean {
    return window.particleAuth?.solana.connecting ?? false;
  }

  get connected(): boolean {
    return window.particleAuth?.solana.connected ?? false;
  }

  get publicKey(): PublicKey | null {
    return window.particleAuth?.solana.publicKey ?? null;
  }

  get selectedAddress(): string | null {
    return window.particleAuth?.solana.selectedAddress ?? null;
  }

  get chainId(): number {
    return window.particleAuth?.solana.chainId ?? 101;
  }

  async connect(param?: ConnectParam): Promise<void> {
    await window.particleAuth?.solana.connect(param);
  }

  async disconnect(): Promise<void> {
    await window.particleAuth?.solana.disconnect();
  }

  signTransaction = async <T extends Transaction | VersionedTransaction>(
    transaction: T,
    chainId?: number
  ): Promise<T> => {
    throw new Error('signMessage not implemented.');
  };

  signAllTransactions = async <T extends Transaction | VersionedTransaction>(
    transactions: T[],
    chainId?: number
  ): Promise<T[]> => {
    throw new Error('signMessage not implemented.');
  };

  signAndSendTransaction = async <T extends Transaction | VersionedTransaction>(
    transaction: T,
    chainId?: number
  ): Promise<{ signature: string }> => {
    throw new Error('signMessage not implemented.');
  };

  signMessage = async (message: Uint8Array): Promise<{ signature: Uint8Array }> => {
    throw new Error('signMessage not implemented.');
  };

  switchChain = async (chainId: number) => {
    await window.particleAuth?.solana.switchChain(chainId);
  };

  // eslint-disable-next-line @typescript-eslint/no-explicit-any
  async request(data: Partial<RpcInput>): Promise<any> {
    return window.particleAuth?.solana.request(data);
  }
}
