import { AuthCoreEvent, hasMasterPassword, hasPaymentPassword, particleAuth } from '@particle-network/auth-core';
import React, { useCallback, useEffect, useRef } from 'react';
import { useModalOptions, useParticleAuth } from '.';
import { isClient } from '../utils';
import { isSocialAuthLogin } from '../utils/isSocialLogin';
import { useAuthCore, useConnect, useEthereum, useSolana } from './hooks';

const ProviderInject = ({ children }: React.PropsWithChildren) => {
  const { provider } = useEthereum();

  const { userInfo } = useParticleAuth();

  const { wallet } = useSolana();

  const { connect } = useConnect();

  const injectRef = useRef(false);

  const { modalOptions } = useModalOptions();

  const {
    needRestoreWallet,
    openAccountAndSecurity,
    openSetMasterPassword,
    openChangeMasterPassword,
    openRestoreByMasterPassword,
    openSetPaymentPassword,
    openChangePaymentPassword,
    openSetSecurityAccount,
    openLinkLoginAccount,
  } = useAuthCore();

  if (isClient() && !injectRef.current) {
    if (window.particle) {
      window.particle.ethereum = provider;
      window.particle.solana = wallet;
    } else {
      window.particle = { ethereum: provider, solana: wallet };
    }
    injectRef.current = true;
    const socialLogin = isSocialAuthLogin();
    window.particle.ethereum.isSocialConnecting = socialLogin;
    window.particle.solana.isSocialConnecting = socialLogin;
    console.log('particle provider inject', provider, wallet);
    window.dispatchEvent(new CustomEvent('particle:authProviderInjected'));
  }

  useEffect(() => {
    const onRequestAccount = ({ resolve, reject, param }: any) => {
      console.log('particle auth connect', param);
      connect(param)
        .then((userInfo) => {
          resolve(userInfo);
        })
        .catch((error) => {
          reject(error);
        });
    };
    particleAuth.on(AuthCoreEvent.RequestEthereumAccount, onRequestAccount);
    particleAuth.on(AuthCoreEvent.RequestSolanaAccount, onRequestAccount);
    return () => {
      particleAuth.off(AuthCoreEvent.RequestEthereumAccount, onRequestAccount);
      particleAuth.off(AuthCoreEvent.RequestSolanaAccount, onRequestAccount);
    };
  }, [connect]);

  const getUserInfo = useCallback(() => {
    return userInfo;
  }, [userInfo]);

  useEffect(() => {
    if (window.particle) {
      window.particle._internal = {
        needRestoreWallet,
        openAccountAndSecurity,
        openSetMasterPassword,
        openChangeMasterPassword,
        openRestoreByMasterPassword,
        openSetPaymentPassword,
        openChangePaymentPassword,
        openSetSecurityAccount,
        openLinkLoginAccount,
        hasMasterPassword,
        hasPaymentPassword,
        getUserInfo,
      };
    }
  }, [
    needRestoreWallet,
    openAccountAndSecurity,
    openSetMasterPassword,
    openChangeMasterPassword,
    openRestoreByMasterPassword,
    openSetPaymentPassword,
    openChangePaymentPassword,
    openSetSecurityAccount,
    openLinkLoginAccount,
    getUserInfo,
  ]);

  useEffect(() => {
    //https://eips.ethereum.org/EIPS/eip-6963
    if (provider && typeof window !== 'undefined' && modalOptions.supportEIP6963 !== false) {
      const info = {
        uuid: 'cf589558-848b-4b1b-9ee7-800b5bbc07da',
        name: 'Particle Auth',
        icon: 'https://static.particle.network/logo-small.png',
        rdns: 'network.particle',
      };
      const announceProvider = () => {
        window.dispatchEvent(
          new CustomEvent('eip6963:announceProvider', {
            detail: Object.freeze({
              provider,
              info,
            }),
          })
        );
      };
      window.addEventListener('eip6963:requestProvider', announceProvider);
      announceProvider();
      return () => {
        window.removeEventListener('eip6963:requestProvider', announceProvider);
      };
    }
  }, [provider, modalOptions.supportEIP6963]);

  return <>{children}</>;
};

export default ProviderInject;
