import { Button, Modal } from 'antd';
import React from 'react';
import { PromptSettingType } from '../../../api/model/bundle';
import CircleClose from '../../../components/icon/circle-close';
import { useAuthCoreModal, useParticleAuth, useTranslation } from '../../../context';
import styles from './index.less';
export interface AccountTipModalProps {
  visible: boolean;
  confirm?: () => void;
}

const AccountTipModal = ({ modal }: { modal: AccountTipModalProps }) => {
  const { visible, confirm } = modal;
  const { t } = useTranslation();
  const { setPaymentPassword, showAccountTipModal, modalOptions } = useParticleAuth();
  const { authCoreModal } = useAuthCoreModal();
  const setPassword = () => {
    showAccountTipModal({
      visible: false,
    });
    setPaymentPassword();
  };

  const continueConfirm = () => {
    confirm?.();
    showAccountTipModal({
      visible: false,
    });
  };

  return (
    <>
      <style>{styles as unknown as string}</style>
      <Modal
        open={visible}
        closable={false}
        centered={true}
        okButtonProps={{ style: { display: 'none' } }}
        cancelButtonProps={{ style: { display: 'none' } }}
        width={'90%'}
        getContainer={() => {
          return authCoreModal.rootBody as HTMLElement;
        }}
      >
        <div className='wrong-password-content'>
          <CircleClose
            className='close-icon'
            onClick={() =>
              showAccountTipModal({
                visible: false,
              })
            }
          />
          <h3 className='modal-title m-margin-top-20'>{t('account.account_modal1_title')}</h3>
          <div className='tip'>{t('account.account_modal1_tip')}</div>
          <Button className='btn' onClick={setPassword}>
            {t('account.account_modal1_btn1')}
          </Button>
          {
            // 强制设置支付密码，隐藏跳过按钮
            modalOptions.promptSettingConfig?.promptPaymentPasswordSettingWhenSign !==
              PromptSettingType.everyAndNotSkip && (
              <Button className='btn cancel' onClick={continueConfirm}>
                {t('account.account_modal1_btn2')}
              </Button>
            )
          }
        </div>
      </Modal>
    </>
  );
};

export default AccountTipModal;
