import {
  AuthCoreEvent,
  getSecurityAccount,
  hasMasterPassword,
  hasPaymentPassword,
  isConnected,
  isNeedRestoreWallet,
  particleAuth,
} from '@particle-network/auth-core';
import type { WalletConfig, WalletEntryPlugin } from '@particle-network/wallet';
import { useCallback, useEffect, useState } from 'react';
import { useCustomNavigate, useParticleAuth, useTranslation } from '..';
import { AuthError } from '../../api/model/authError';
import globalConfig from '../../common/config';
import { AuthPage } from '../../components/customRouter';
import { PageType } from '../../pages/account/accountVerify/config';
import { sendAnalyticsActive } from '../../utils/sendAnalyticsActive';
import type { OpenBuyOptions, OpenBuyOptionsKeys } from '../types';
import useMessage from './useMessage';

interface OpenWalletOptions {
  windowSize?: 'small' | 'large';
  pathName?: string;
  query?: {
    [key: string]: unknown;
  };
  topMenuType?: 'close' | 'fullscreen';
}

export const useAuthCore = () => {
  const navigate = useCustomNavigate();
  const { userInfo, modalOptions, walletEntryPlugin } = useParticleAuth();
  const [needRestoreWallet, setNeedRestoreWallet] = useState(isNeedRestoreWallet());
  const { t } = useTranslation();
  const message = useMessage();

  const openAccountAndSecurity = useCallback(() => {
    navigate(AuthPage.AccountSecurity);
  }, [navigate]);

  const openSetMasterPassword = useCallback(() => {
    if (hasMasterPassword()) {
      throw new Error('Master password already exists.');
    }
    navigate('account/master-password');
  }, [navigate]);

  const openChangeMasterPassword = useCallback(() => {
    if (!hasMasterPassword()) {
      throw new Error('Master password not exist, please set it first!');
    }
    if (isNeedRestoreWallet()) {
      throw new Error('Wallet not restore, please restore it by master password first!');
    }
    navigate('account/master-password/change');
  }, [navigate]);

  const openRestoreByMasterPassword = useCallback(() => {
    if (!hasMasterPassword()) {
      throw new Error('Master password not exists.');
    }
    if (!isNeedRestoreWallet()) {
      throw new Error('Wallet has been restore.');
    }
    navigate(AuthPage.MasterPasswordVerify);
  }, [navigate]);

  const openWallet = useCallback(
    (options?: OpenWalletOptions) => {
      const { windowSize, pathName, query, topMenuType } = options || {};
      if (!isConnected()) {
        throw AuthError.notLogin();
      }
      const walletEntry: WalletEntryPlugin = walletEntryPlugin || window.walletEntryPlugin;
      if (!walletEntry) {
        const errorMsg = t('error.wallet_not_activated');
        message.warning(errorMsg);
        throw new Error(errorMsg);
      }
      walletEntry.openWallet({
        windowSize: windowSize || 'large',
        pathName: pathName || '/',
        query,
        topMenuType,
      });
      sendAnalyticsActive('open_wallet');
    },
    [walletEntryPlugin, t]
  );

  const getWalletIFrame = useCallback(
    (options?: WalletConfig): HTMLIFrameElement => {
      if (!isConnected()) {
        throw AuthError.notLogin();
      }
      if (modalOptions.wallet === false) {
        throw new Error('The embedded wallet is disabled.');
      }
      if (!walletEntryPlugin) {
        throw new Error('wallet plugin load failed.');
      }
      return walletEntryPlugin.getWalletIFrame(options);
    },
    [modalOptions.wallet, walletEntryPlugin]
  );

  const openBuy = useCallback(
    (options?: OpenBuyOptions) => {
      let url = `${globalConfig.env.buyUrl}/?language=${modalOptions.language || 'en'}&theme=${
        modalOptions.themeType || 'light'
      }`;
      if (modalOptions.wallet && modalOptions.wallet?.customStyle) {
        const { light, dark } = modalOptions.wallet.customStyle;
        url += `&customStyle=${encodeURIComponent(Buffer.from(JSON.stringify({ light, dark })).toString('base64'))}`;
      }

      if (options) {
        Object.keys(options).forEach((key) => {
          const value = options[key as OpenBuyOptionsKeys];
          if (value) {
            url += `&${key}=${encodeURIComponent(value)}`;
          }
        });
      }
      if (typeof window !== 'undefined') {
        window.open(url);
      }
    },
    [modalOptions.wallet, modalOptions.themeType, modalOptions.language]
  );

  const openSetPaymentPassword = useCallback(
    (securityAccountEmailOrPhone: string) => {
      const securityAccount = getSecurityAccount();
      if (!securityAccount.email && !securityAccount.phone) {
        throw new Error('Please set security account first!');
      }
      if (hasPaymentPassword()) {
        throw new Error('Payment password already exists.');
      }

      if (
        securityAccountEmailOrPhone &&
        securityAccountEmailOrPhone !== securityAccount.email &&
        securityAccountEmailOrPhone !== securityAccount.phone
      ) {
        throw new Error('Invalid security account.');
      }
      navigate('account/set-password', {
        state: {
          account: securityAccountEmailOrPhone,
        },
      });
    },
    [navigate]
  );

  const openChangePaymentPassword = useCallback(() => {
    if (!hasPaymentPassword()) {
      throw new Error('Payment password not exists.');
    }
    navigate('account/change-password');
  }, [navigate]);

  const openSetSecurityAccount = useCallback(() => {
    const securityAccount = getSecurityAccount();
    if (securityAccount.email && securityAccount.phone) {
      throw new Error('Security account already exists.');
    }

    const showSwitch = !securityAccount.email && !securityAccount.phone;

    if ((securityAccount.email && !securityAccount.phone) || (!securityAccount.email && securityAccount.phone)) {
      navigate('/account/verify', {
        state: {
          account: securityAccount.email || securityAccount.phone,
          authType: securityAccount.email ? 'phone' : 'email',
          redirectUrl: '/account/security',
          pageType: PageType.VerifySecurityAccount_SetSecurityAccount,
        },
      });
    } else {
      if (userInfo?.email && !securityAccount.phone) {
        navigate('account/bind', {
          state: { showSwitch, accountType: 'phone', redirectUrl: AuthPage.AccountSecurity },
        });
      } else if (!securityAccount.email) {
        navigate('account/bind', {
          state: { showSwitch, accountType: 'email', redirectUrl: AuthPage.AccountSecurity },
        });
      } else {
        navigate('account/bind', {
          state: { showSwitch, accountType: 'phone', redirectUrl: AuthPage.AccountSecurity },
        });
      }
    }
  }, [navigate, userInfo]);

  const openLinkLoginAccount = useCallback(() => {
    const securityAccount = getSecurityAccount();
    if (!securityAccount.email && !securityAccount.phone) {
      throw new Error('Please set security account first!');
    }
    navigate(AuthPage.LoginAccount);
  }, [navigate]);

  useEffect(() => {
    if (userInfo) {
      setNeedRestoreWallet(isNeedRestoreWallet());
    } else {
      setNeedRestoreWallet(false);
    }
  }, [userInfo]);

  useEffect(() => {
    const onWalletRestored = () => {
      setNeedRestoreWallet(isNeedRestoreWallet());
    };
    particleAuth.on(AuthCoreEvent.ParticleWalletRestored, onWalletRestored);
    return () => {
      particleAuth.off(AuthCoreEvent.ParticleWalletRestored, onWalletRestored);
    };
  }, []);

  return {
    userInfo,
    needRestoreWallet,
    openAccountAndSecurity,
    openSetMasterPassword,
    openChangeMasterPassword,
    openRestoreByMasterPassword,
    openSetPaymentPassword,
    openChangePaymentPassword,
    openSetSecurityAccount,
    openLinkLoginAccount,
    openWallet,
    getWalletIFrame,
    openBuy,
  };
};
