import {
  EvmRpcMethod,
  getEVMChainInfoById,
  getSmartAccountAddress,
  tronAddressFromHex,
  type ERC4337Options,
} from '@particle-network/auth-core';
import { isTron } from '../utils/chain-utils';

type Options = {
  chainId?: number;
  method?: EvmRpcMethod;
  erc4337?: ERC4337Options;
};

export const getEVMPublicAddress = async ({ chainId, erc4337, method }: Options): Promise<string> => {
  const id = chainId || Number(window.particleAuth?.ethereum.chainId);
  let address;
  if (erc4337 && method !== EvmRpcMethod.ethSendTransaction) {
    address = await getSmartAccountAddress(erc4337);
  } else {
    if (typeof window !== 'undefined') {
      address = window.particleAuth?.ethereum.selectedAddress;
    }
  }

  if (isTron(getEVMChainInfoById(id))) {
    address = tronAddressFromHex(address);
  }
  return address || '';
};
