import {
  ApiError,
  AuthType,
  bindLoginAccount,
  getBindSecurityAccounCaptcha,
  getCloudflareTurnstileResponse,
  getSecurityAccountCaptcha,
  getSecurityAccountToken,
  setPaymentPassword,
  syncUserInfo,
  thirdpartyAuth,
  type SocialAuthType,
} from '@particle-network/auth-core';
import { useInterval, useRequest } from 'ahooks';
import { Button, Modal } from 'antd';
import base64url from 'base64url';
import throttle from 'lodash/throttle.js';
import React, { useCallback, useEffect, useMemo, useRef, useState } from 'react';
import SVGIcon from '../../../components/icon/svg-icon';
import PowerFooter from '../../../components/power-footer';
import AuthCode from '../../../components/react-auth-code-input';
import {
  useAuthCoreModal,
  useCustomNavigate,
  useCustomRouter,
  useParticleAuth,
  useTranslation,
} from '../../../context';
import { useCustomize } from '../../../context/hooks/useCustomize';
import useMessage from '../../../context/hooks/useMessage';
import { displayEmail, displayPhone } from '../../../utils/common-utils';
import { getAccountList } from '../../loginAccount';
import { PageType } from './config';
import styles from './index.less';

interface IParams {
  account: string; // 进入页面时会给这个账户发送验证码，这个账户可能是安全账号，也可能是要绑定的账号
  pageType: PageType;
  unbindAccount: string; //解绑手机、邮箱时使用
  authType: AuthType;
  verifyToken: string;
  redirectUrl: string;
  password: string; // 重置密码时需要
}

const AccountVerify = (props: any) => {
  const navigate = useCustomNavigate();
  const { t } = useTranslation();

  const state = props;

  const message = useMessage();

  const { customRouter } = useCustomRouter();

  const { themeType, language } = useCustomize();

  const { account, pageType, authType, verifyToken, redirectUrl, password } = state as IParams;

  const [code, setCode] = useState<string>('');

  const [interval, setInterval] = useState<number | undefined>(1000);

  const [countdown, setCountdown] = useState(60);

  const [errorTip, setErrorTip] = useState<string>();
  const { authCoreModal } = useAuthCoreModal();
  const { userInfo } = useParticleAuth();
  const [verifyLoading, setVerifyLoading] = useState(false);

  const inputRef = useRef(null);

  useInterval(() => {
    if (countdown > 0) {
      const result = countdown - 1;
      setCountdown(result);
      if (result === 0) {
        setInterval(undefined);
      }
    }
  }, interval);

  // 获取验证码
  const { run: runGetCode } = useRequest(
    async (params) => {
      console.log('get code', params);
      if (pageType === PageType.BindLoginAccount) {
        params = {
          [authType as string]: account,
          // eslint-disable-next-line camelcase
          cf_turnstile_response: params.cf_turnstile_response,
        };
        return getBindSecurityAccounCaptcha(params).then((res) => {
          return res;
        });
      } else {
        return getSecurityAccountCaptcha(params.verifyCodeMethod);
      }
    },
    {
      manual: true,
      onSuccess: (result, params) => {
        if (!interval) {
          setCountdown(60);
          setInterval(1000);
        }
      },
      onError: (error) => {
        setCountdown(0);
        setInterval(undefined);
      },
    }
  );

  // 验证安全账号，获取token
  const { runAsync: securityAccountsVerifyRequest } = useRequest(getSecurityAccountToken, {
    manual: true,
    onBefore: () => {
      setVerifyLoading(true);
    },
    onError: (error: any) => {
      if (error?.error_code === ApiError.InvalidCode) {
        setErrorTip(t('login.invalid_code') as string);
      } else if (error?.error_code === ApiError.ResendCode) {
        setErrorTip(t('login.please_send_again') as string);
      } else {
        error.message && message.error(error.message);
      }
    },
    onFinally: () => {
      setTimeout(() => {
        setVerifyLoading(false);
      }, 2000);
    },
  });

  // 绑定登录邮箱或手机号
  const { loading: loadBindingLoading, runAsync: loginBindingsAsyncRequest } = useRequest(
    async (params) => {
      console.log('loginBindingsAsyncRequest', params);
      return bindLoginAccount(params).then(async () => {
        await syncUserInfo();
      });
    },
    {
      manual: true,
      onSuccess: (data) => {
        message.success(t('new.bind_login_account_success'));
      },
      onError: (error: any) => {
        console.log('loginBindings', error);
        let messageValue = error.message;
        if (error?.error_code === ApiError.InvalidCode) {
          setErrorTip(t('login.invalid_code') as string);
          return;
        } else if (error?.error_code === ApiError.ResendCode) {
          setErrorTip(t('login.please_send_again') as string);
          return;
        } else if (error.error_code === 20109) {
          messageValue = authType == AuthType.email ? t('error.server_email_20109') : t('error.server_phone_20109');
        } else {
          messageValue = error?.extra?.[0] || error.message;
        }

        Modal.error({
          title: messageValue,
          wrapClassName: 'auth-core-modal-error',
          getContainer: () => {
            return authCoreModal.rootBody as HTMLElement;
          },
          onOk: () => {
            (inputRef.current as any)?.clear();
            if (error.error_code === 20109) {
              // back
              navigate(-1);
            }
          },
        });
      },
    }
  );

  // 重置支付密码
  const { loading: resetLoading, run: runResetPaymentPassword } = useRequest(setPaymentPassword, {
    manual: true,
    onError: (error: any) => {
      if (error?.error_code === ApiError.InvalidCode) {
        setErrorTip(t('login.invalid_code') as string);
      } else if (error?.error_code === ApiError.ResendCode) {
        setErrorTip(t('login.please_send_again') as string);
      }
    },
    onSuccess: (result, params) => {
      syncUserInfo();

      const paths = ['account/security', 'solana/sign', 'evm-chain/sign'];
      const indexs = paths.map((path) => {
        return customRouter.history?.findIndex((item) => item.path === path) as number;
      });

      const index = indexs.indexOf(Math.max(...indexs));

      navigate(paths[index], { replace: true, back: true });
    },
  });

  // 解绑登录账号
  const { loading: deleteLoginAccountLoading, run: deleteLoginAccount } = useRequest(
    // pnApi.deleteLoginBinding
    () => new Promise((resolve, reject) => {}),
    {
      manual: true,
      onSuccess: (result) => {
        // const _userInfo = tokenProvider.userInfo;
        // _userInfo.passkeys_id = undefined;
        // tokenProvider.userInfo = _userInfo;
        // setUserInfo({
        //     passkeys_id: undefined,
        // });
        // route(-2);
      },
    }
  );

  const verifyCodeMethod = useMemo(() => {
    return account?.includes('@') ? AuthType.email : AuthType.phone;
  }, [account]);

  useEffect(() => {
    requestVerifyCode();
  }, [account]);

  const requestVerifyCode = () => {
    setErrorTip('');
    if (pageType === PageType.BindLoginAccount && authType === AuthType.phone) {
      getCloudflareTurnstileResponse({
        theme: themeType,
        language,
        getContainer: () => {
          return authCoreModal.rootBody as HTMLElement;
        },
      })
        .then((token) => {
          runGetCode({
            // eslint-disable-next-line camelcase
            verify_code_method: verifyCodeMethod,
            // eslint-disable-next-line camelcase
            cf_turnstile_response: token,
          });
        })
        .catch(() => {
          message.error(t('error.server_20112'));
          navigate(-1);
        });
    } else {
      runGetCode({
        // eslint-disable-next-line camelcase
        verifyCodeMethod: verifyCodeMethod,
      });
    }
  };

  const onCodeInputChange = (code: string) => {
    setErrorTip('');
    setCode(code);

    if (code.length === 6) {
      throttleConfirmCode(code);
    }
  };

  const throttleConfirmCode = useCallback(
    throttle(
      (code) => {
        confirmCode(code);
      },
      1000,
      {
        leading: true,
        trailing: false,
      }
    ),
    []
  );

  const resetPassword = (codeValue?: string) => {
    runResetPaymentPassword({
      password,
      verifyCodeMethod: verifyCodeMethod,
      code: codeValue || code,
    });
  };

  const confirmCode = (code = '') => {
    console.log('pageType', pageType);
    if (errorTip) {
      console.log('errorTip', errorTip);
      return;
    }
    let data: any = { code };
    if (pageType === PageType.ResetPaymentPassword) {
      resetPassword(code);
    } else if (pageType == PageType.VerifySecurityAccount_SetSecurityAccount) {
      // 绑定安全账号时前置验证安全账号
      securityAccountsVerifyRequest({
        verifyCodeMethod: verifyCodeMethod,
        code,
      }).then((verifyToken) => {
        navigate('/account/bind', {
          replace: true,
          state: {
            accountType: authType,
            verifyToken,
            redirectUrl,
          },
        });
      });
    } else if (pageType === PageType.VerifySecurityAccount_BindLoginAccount) {
      // 绑定登录账号时前置验证安全账号
      securityAccountsVerifyRequest({
        verifyCodeMethod: verifyCodeMethod,
        code,
      }).then((verifyToken) => {
        const accountList = getAccountList({
          userInfo,
          t,
        });
        const { id, value } = accountList.find((item) => item.type === authType) || {};
        // 验证成功后，根据 authType 执行不同逻辑
        // AuthType.phone，AuthType.email 跳转页面，其他的执行auth签名跳转登录授权
        if (authType === AuthType.phone || authType === AuthType.email || value || id) {
          navigate('/login-account/bind', {
            state: {
              authType,
              verifyToken,
            },
            replace: true,
          });
        } else {
          console.log('绑定第三方 页面跳转');
          const appState = base64url.encode(JSON.stringify({ verifyToken, purpose: 'bindLoginAccount' }));
          thirdpartyAuth({
            authType: authType as SocialAuthType,
            appState,
            prompt: 'select_account',
          }).catch((error) => {
            message.error(error?.message || error);
          });
        }
      });
    } else if (pageType === PageType.BindLoginAccount) {
      // eslint-disable-next-line camelcase
      data = { ...data, security_account_verify_token: verifyToken, [authType as string]: account };
      loginBindingsAsyncRequest(data).then((res) => {
        navigate(-2);
      });
    } else if (pageType === PageType.UnbindLoginAccount) {
      // passkeys
    }
  };

  const formatDisplayAccount = (): string => {
    if (account.includes('@')) {
      return displayEmail(account);
    } else {
      return displayPhone(account);
    }
  };

  useEffect(() => {
    const elements = document.getElementsByClassName('react-input-code');
    const contextmenuEvent = (e: any) => e.preventDefault();
    elements[0]?.addEventListener('contextmenu', contextmenuEvent);
    return () => {
      elements[0]?.removeEventListener('contextmenu', contextmenuEvent);
    };
  }, []);

  const handleCodeInputFocus = () => {
    const elements = document.getElementsByClassName('input-code-item');
    //@ts-ignore
    elements[Math.min(code.length, 5)]?.focus();
  };

  return (
    <div className='set-viery-container'>
      <style>{styles as unknown as string}</style>
      <div className='account-header'>
        <SVGIcon
          className='icon-navigation-back'
          name='circle_back'
          onClick={() => {
            navigate(-1);
          }}
        />
      </div>
      <h2 className='set-email-title'>{t('account.enter_code')}</h2>
      <p className='set-email-desc-2'>{formatDisplayAccount()}</p>

      <div onClick={handleCodeInputFocus}>
        <AuthCode
          containerClassName='react-input-code'
          inputClassName='input-code-item'
          allowedCharacters='numeric'
          length={6}
          ref={inputRef}
          placeholder={' '}
          onChange={onCodeInputChange}
        />
      </div>

      {errorTip && <div className='code-error'>{errorTip}</div>}

      <Button
        className='account-submit-btn'
        loading={resetLoading || verifyLoading || loadBindingLoading || deleteLoginAccountLoading}
        onClick={() => {
          setTimeout(() => {
            confirmCode(code);
          });
        }}
        disabled={code.length !== 6}
      >
        {t('common.confirm')}
      </Button>

      <Button className='send-again' onClick={requestVerifyCode} disabled={countdown > 0}>
        {countdown > 0 ? `${t('login.send_again')} (${countdown}s)` : t('login.send_again')}
      </Button>
      <div
        className='back'
        onClick={() => {
          navigate(-1);
        }}
      >
        <SVGIcon className='arrow1-icon' name='arrow1_icon' />
        <span>{t('login.back')}</span>
      </div>

      <PowerFooter />
    </div>
  );
};

export default AccountVerify;
