import type { LoginAuthorization, SolanaAuthorization, UserInfo } from '@particle-network/auth-core';
import {
  AuthCoreEvent,
  EvmRpcMethod,
  SolanaRpcMethod,
  connect,
  getConnectCaptcha,
  getUserInfo,
  hasPaymentPassword,
  isConnected,
  particleAuth,
  syncUserInfo,
} from '@particle-network/auth-core';
import type { WalletEntryPlugin } from '@particle-network/wallet';
import { useAsyncEffect, useSetState, useSize } from 'ahooks';
import base58 from 'bs58';
import { EventEmitter } from 'events';
import get from 'lodash/get';
import React, { createContext, useCallback, useContext, useEffect, useMemo, useRef, useState } from 'react';
import root from 'react-shadow';
import { AuthError } from '../api/model/authError';
import CustomRouter, { AuthPage } from '../components/customRouter';
import FocusTrap from '../components/focusTrap';
import ParticleModal from '../components/particle-modal';
import AuthCoreModalContainer from '../components/particle-modal/authCoreModalContainer';
import { dictionaries } from '../locales';
import type { AccountTipModalProps } from '../pages/account/accountTipModal';
import type { PaymentVerify } from '../pages/account/paymentVerifyModal';
import type { WrongPasswordInfo } from '../pages/account/wrongPasswordModal';
import { isServer } from '../utils';
import authorizeUtils from '../utils/authorizeUtils';
import { isSolana } from '../utils/chain-utils';
import { toHexPrefixString } from '../utils/common-utils';
import { sendAnalyticsActive } from '../utils/sendAnalyticsActive';
import { getVersion } from '../utils/version';
import ProviderInject from './providerInject';
import type {
  AuthCoreModalOptions,
  ConnectionStatus,
  IAuthCoreModal,
  ICustomRouter,
  Language,
  NavigateOptions,
  SocialConnectCallback,
  ViewType,
} from './types';
import { AuthCoreModalEvent } from './types';
import Web3ModalProvider from './web3ModalProvider';

const IframeWalletPlugin = React.lazy(() => import('./iframeWalletPlugin'));
const AccountTipModal = React.lazy(() => import('../pages/account/accountTipModal'));
const PaymentVerifyModal = React.lazy(() => import('../pages/account/paymentVerifyModal'));
const WrongPasswordModal = React.lazy(() => import('../pages/account/wrongPasswordModal'));
const SelectSecurityAccount = React.lazy(() => import('../pages/account/selectSecurityAccount'));

export const defaultModalOptions: Partial<AuthCoreModalOptions> = {
  themeType: 'light',
  language: 'en',
  fiatCoin: 'USD',
  promptSettingConfig: {
    promptPaymentPasswordSettingWhenSign: 1,
    promptMasterPasswordSettingWhenLogin: 0,
  },
  customStyle: {
    modalWidth: 400,
    modalHeight: 650,
  },
  wallet: { visible: true, themeType: 'light' },
};

interface GlobalState {
  connectionStatus: ConnectionStatus;
  setConnectionStatus: (status: ConnectionStatus) => void;
  userInfo?: UserInfo;
  modalOptions: AuthCoreModalOptions;
  setModalOptions: (options: any) => void;
  showSelectSecurityAccount: (display: boolean, state?: any) => void;
  setWrongPassword: (info: WrongPasswordInfo) => void;
  setPaymentVerify: (props: PaymentVerify) => void;
  showAccountTipModal: (props: AccountTipModalProps) => void;
  setPaymentPassword: () => void;
  loginSuccessRedirectToApp: () => void;
  events: EventEmitter;
  authCoreModal: IAuthCoreModal;
  setAuthCoreModal: (params: any) => void;
  customRouter: ICustomRouter;
  setCustomRouter: (params: any) => void;
  customNavigate: (url: string | number, options?: NavigateOptions) => void;
  setLoginAuthorization: (loginAuthorization?: LoginAuthorization) => void;
  authCoreModalClose: () => void;
  socialConnectCallback?: SocialConnectCallback;
  setSocialConnectCallback: (callback?: SocialConnectCallback) => void;
  walletEntryPlugin?: WalletEntryPlugin;
  lang: string;
  setLang: (value: Language) => void;
}

export const GlobalContext = createContext<GlobalState | null>(null);

const events = new EventEmitter();
events.setMaxListeners(100);

export const AuthCoreContextProvider = (props: { options: AuthCoreModalOptions; children: React.ReactNode }) => {
  const [userInfo, _setUserInfo] = useState<UserInfo | undefined>();
  const userInfoRef = useRef<UserInfo | undefined>(userInfo);

  const setUserInfo = (info: UserInfo | undefined) => {
    _setUserInfo(info);
    userInfoRef.current = info;
  };

  const [lang, setLang] = useState<Language>('en');
  const rootRef = React.useRef<HTMLDivElement>(null);

  const [styles, setStyles] = useState<any>({
    styleList: [
      {
        name: 'antStyle',
        loadStyle: () => import('../../../../node_modules/antd/dist/antd.css'),
        styleContent: '',
      },
      {
        name: 'globalStyle',
        loadStyle: () => import('../common/style/global.scss'),
        styleContent: '',
      },
      {
        name: 'scrollStyle',
        loadStyle: () => import('../common/style/scroll.scss'),
        styleContent: '',
      },
      {
        name: 'themeStyle',
        loadStyle: () => import('../common/style/theme.scss'),
        styleContent: '',
      },
    ],
  });

  const [modalOptions, setModalOptions] = useSetState<AuthCoreModalOptions>(props.options);

  const [selectSecurityAccount, setSelectSecurityAccount] = useState(false);

  const [verifySecurityAccountState, setVerifySecurityAccountState] = useState<any>();

  const [wrongPassword, setWrongPassword] = useState<WrongPasswordInfo>({ visible: false });

  const [accountTipModal, setAccountTipModal] = useState<AccountTipModalProps>({
    visible: false,
  });

  const initParticleAuthRef = React.useRef<any>(false);

  const [paymentVerify, setPaymentVerify] = useState<PaymentVerify>({ visible: false });

  const [loginAuthorization, _setLoginAuthorization] = useState<LoginAuthorization>();

  const loginAuthorizationRef = useRef<any>(null);

  const setLoginAuthorization = (data?: LoginAuthorization) => {
    _setLoginAuthorization(data);
    loginAuthorizationRef.current = data;
  };

  const [connectionStatus, setConnectionStatus] = useState<ConnectionStatus>('loading');

  const showSelectSecurityAccount = (display: boolean, state?: any) => {
    setVerifySecurityAccountState(state);
    setSelectSecurityAccount(display);
  };

  const showAccountTipModal = (props: AccountTipModalProps) => {
    setAccountTipModal(props);
  };

  const loadStyles = () => {
    const fns = styles.styleList.map((item: any) => {
      return item?.loadStyle?.();
    });
    Promise.all(fns).then((styles: any) => {
      const styleList = styles.map((item: any) => {
        return {
          styleContent: item.default,
        };
      });
      setStyles({
        styleList,
      });
    });
  };

  const [customRouter, setCustomRouter] = useSetState<ICustomRouter>({ path: '/' });
  const [authCoreModal, setAuthCoreModal] = useSetState<IAuthCoreModal>({
    viewType: 'mobile' as ViewType,
    particleModalVisible: false,
    rootModalContainer: null,
    rootBody: null,
    closable: true,
  });

  const customNavigate = useCallback(
    (url: string | number, options?: NavigateOptions) => {
      if (typeof url === 'string' && url !== '/') {
        url = url.replace(/^\//, '');
      }

      console.log('customNavigate', url, options);

      let { history } = customRouter;

      if (!history) {
        history = [];
      }

      if (options?.back === true) {
        if (
          typeof url === 'string' &&
          !!history.find((item: any) => item.path.replace(/^\//, '') === (url as string).replace(/^\//, ''))
        ) {
          url = history.findIndex((item) => item.path === url) - history.length + 1;
        }
      }

      if (typeof url === 'string') {
        if (url !== AuthPage.Login && url !== AuthPage.Index && !isConnected()) {
          throw AuthError.notLogin();
        }

        const { replace = false, state = {} } = options || {};

        if (history.length && replace) {
          history.pop();
        }

        history.push({
          path: url,
          state,
          updateAt: new Date().getTime(),
        });

        setCustomRouter({
          path: url,
          replace,
          state,
          history,
        });
      } else if (typeof url === 'number' && history.length > 0) {
        for (let i = 0; i < Math.abs(url); i++) {
          history.pop();
        }

        if (history.length === 0) {
          setAuthCoreModal({
            particleModalVisible: false,
          });
          return;
        }

        const route = history[history.length - 1];
        setCustomRouter({
          path: route?.path,
          replace: false,
          state: route?.state,
          history,
        });
      }
    },
    [customRouter, setAuthCoreModal, setCustomRouter]
  );

  const setPaymentPassword = useCallback(() => {
    if (userInfo?.security_account?.has_set_payment_password) {
      return;
    }
    if (!userInfo?.security_account?.email && !userInfo?.security_account?.phone) {
      customNavigate('/account/bind', {
        state: {
          accountType: userInfo?.email ? 'phone' : 'email',
          showSwitch: true,
          redirectUrl: '/account/set-password',
        },
      });
      return;
    }
    if (userInfo?.security_account?.email && userInfo?.security_account?.phone) {
      showSelectSecurityAccount(true);
    } else {
      customNavigate('/account/set-password', {
        state: {
          account: userInfo?.security_account?.email || userInfo?.security_account?.phone,
        },
      });
    }
  }, [userInfo, customNavigate]);

  const loginSuccessRedirectToApp = useCallback(() => {
    console.log('login success', 'redirectToApp');

    const userInfo = userInfoRef.current;
    const loginAuthorization = loginAuthorizationRef.current;

    let authorizeResult;
    if (loginAuthorization && authorizeUtils.isNeedAuthorize(loginAuthorization)) {
      if (hasPaymentPassword()) {
        if (isSolana(loginAuthorization.chain)) {
          const message = (loginAuthorization.authorization as SolanaAuthorization).message;
          customNavigate(AuthPage.SolanaSign, {
            state: {
              method: SolanaRpcMethod.signMessage,
              param: base58.decode(message),
              loginAuthorizationSign: true,
            },
          });
        } else {
          authorizeUtils
            .generateMessage(loginAuthorization.chain, modalOptions.erc4337)
            .then((message) => {
              customNavigate(AuthPage.EvmSign, {
                state: {
                  method: loginAuthorization.authorization.uniq
                    ? EvmRpcMethod.personalSignUniq
                    : EvmRpcMethod.personalSign,
                  param: toHexPrefixString(message),
                  loginAuthorizationSign: true,
                },
              });
            })
            .catch((error) => {
              events.emit(AuthCoreModalEvent.LoginFail, error);
            });
        }
        return;
      } else {
        authorizeUtils
          .authorize(loginAuthorization, modalOptions.erc4337)
          .then((result) => {
            events.emit(AuthCoreModalEvent.LoginSuccess, {
              ...userInfo,
              authorization: result,
            });
          })
          .catch((error) => {
            events.emit(AuthCoreModalEvent.LoginFail, error);
          });
      }
    } else {
      events.emit(AuthCoreModalEvent.LoginSuccess, {
        ...(userInfo ?? getUserInfo()),
        authorization: authorizeResult,
      });
    }
  }, [customNavigate, userInfo, modalOptions.erc4337, loginAuthorization]);

  const authCoreModalClose = () => {
    if (events.listenerCount(AuthCoreModalEvent.SignResponse) > 0) {
      events.emit(AuthCoreModalEvent.SignResponse, {
        error: AuthError.userRejectedRequest(),
      });
    }

    if (events.listenerCount(AuthCoreModalEvent.LoginSuccess)) {
      if (customRouter?.path === AuthPage.Login || !userInfo) {
        events.emit(AuthCoreModalEvent.LoginFail, AuthError.userCancelOperation());
      } else {
        events.emit(AuthCoreModalEvent.LoginSuccess, userInfo);
      }
    }

    setAuthCoreModal({
      particleModalVisible: false,
    });
  };

  const getPageHTML = () => {
    let htmlContent = rootRef.current?.outerHTML || '';
    const path = rootRef.current?.getAttribute('data-path');
    htmlContent = Buffer.from(htmlContent).toString('base64');
    console.log('getPageHTML', path, htmlContent.substring(0, 30) + '...');
    return htmlContent;
  };

  const fontStyle = useMemo(() => {
    let style = '';
    if (!isServer()) {
      const bodyFontFamily = window.getComputedStyle(window.document.body).fontFamily;
      const fontFamily =
        props.options?.customStyle?.fontFamily ||
        bodyFontFamily ||
        '"PingFang SC", "Helvetica Neue", "Helvetica", "Arial", sans-serif';

      style = ` 
                            .particle-auth-core-shadow-root {
                                --auth-core-font-family: ${fontFamily};
                            }
                        `;
    }

    return style;
  }, [props.options?.customStyle?.fontFamily]);

  // 自定义 主题
  const customThemeStyle = useMemo(() => {
    const themeType = modalOptions.themeType;
    const customStyle = modalOptions.customStyle || {};

    const theme = {
      ...Object.fromEntries(
        Object.keys(customStyle || {})
          .filter((key) => key !== 'theme')
          .map((key) => {
            return [key, (customStyle as any)?.[key] || ''];
          })
      ),

      ...(customStyle?.theme?.[themeType === 'light' ? 'light' : 'dark'] || {}),
    };

    const styleContent = Object.keys(theme)
      .filter((key) => key !== 'fontFamily')
      .map((key) => {
        const name = key.replace(/([A-Z])/g, '-$1').toLowerCase();
        let value = theme[key as keyof typeof theme];
        if (!isNaN(Number(value))) {
          value = value + 'px';
        }
        return `--${name}:${value};`;
      })
      .join('\n');

    if (themeType === 'dark') {
      return `.particle-auth-core-shadow-root.dark{
                    ${styleContent}
                }`;
    }

    return `.particle-auth-core-shadow-root{
            ${styleContent}
        }`;
  }, [modalOptions.themeType, modalOptions.customStyle]);

  useEffect(() => {
    setModalOptions({
      ...defaultModalOptions,
      ...props.options,
      promptSettingConfig: {
        ...defaultModalOptions.promptSettingConfig,
        ...props.options.promptSettingConfig,
      },
      customStyle: {
        ...defaultModalOptions.customStyle,
        ...props.options.customStyle,
      },
    });
  }, [props.options, setModalOptions]);

  useEffect(() => {
    // 弹出窗口后 锁定body 滚动
    if (!document.querySelector('.particle-auth-core-body-style')) {
      const style = document.createElement('style');
      style.classList.add('particle-auth-core-body-style');
      const styleContent = `.particle-modal-lock {
                overflow: hidden !important;
            }`;
      style.innerHTML = styleContent;
      document.head.appendChild(style);
    }

    if (authCoreModal.particleModalVisible) {
      document.body.classList.add('particle-modal-lock');
      const rootBody = authCoreModal.rootModalContainer?.querySelector(
        '.root-particle-modal-container .ant-drawer-body,.root-particle-modal-container .ant-modal-body'
      ) as HTMLDivElement;
      setAuthCoreModal({
        rootBody,
      });
    } else {
      // 关闭rootModal弹窗后，延时清空路由
      setTimeout(() => {
        setCustomRouter({
          path: '/',
          history: [],
        });
      }, 100);
      document.body.classList.remove('particle-modal-lock');
    }
  }, [authCoreModal.particleModalVisible, authCoreModal.rootModalContainer]);

  const bodySze = useSize(isServer() ? null : document.body);

  useEffect(() => {
    // 设置viewType 为全局变量 desktop | mobile
    let viewType: ViewType = 'mobile';
    if (document.body.clientWidth >= 600) {
      viewType = 'desktop';
    }
    setAuthCoreModal({
      viewType,
    });
  }, [bodySze]);

  useEffect(() => {
    if (customRouter?.path && customRouter?.path != '/') {
      // 路由 path 不为 / 时，打开rootModal弹窗
      setAuthCoreModal({
        particleModalVisible: true,
      });
    }
  }, [customRouter?.path]);

  useEffect(() => {
    // 设置模态框的宽度和高度，响应移动端视图
    if (rootRef.current) {
      const minWidth = 300;
      const minHeight = 500;
      let modalWidth = Math.max(minWidth, Number(modalOptions.customStyle?.modalWidth));
      let modalHeight = Math.max(minHeight, Number(modalOptions.customStyle?.modalHeight));

      if (authCoreModal.viewType == 'mobile') {
        modalWidth = document.body.clientWidth;
        modalHeight = document.body.clientHeight * 0.86;
      }
      const vm = modalWidth / 100;
      rootRef.current.style.setProperty('--vw', vm + 'px');
      rootRef.current.style.setProperty('--doc-height', modalHeight + 'px');
      rootRef.current.style.setProperty('--doc-width', modalWidth + 'px');
    }
  }, [
    rootRef.current,
    authCoreModal.viewType,
    modalOptions.customStyle?.modalWidth,
    modalOptions.customStyle?.modalHeight,
  ]);

  useEffect(() => {
    setTimeout(() => {
      setLang(modalOptions.language || 'en');
    });
  }, [modalOptions.language]);

  useEffect(() => {
    const onMasterPasswordChange = () => {
      customNavigate(AuthPage.MasterPasswordVerify);
    };
    window?.particleAuth?.on(AuthCoreEvent.MasterPasswordChanged, onMasterPasswordChange);
    return () => {
      window?.particleAuth?.off(AuthCoreEvent.MasterPasswordChanged, onMasterPasswordChange);
    };
  }, [customRouter.history, customNavigate]);

  useEffect(() => {
    const userInfoChanged = (userInfo: UserInfo) => {
      setUserInfo(userInfo);
    };
    const onDisconnect = (error?: Error) => {
      setUserInfo(undefined);
      if (events.listenerCount(AuthCoreModalEvent.SignResponse) > 0) {
        // 如果有监听signResponse事件，说明是在签名中断，需要触发signResponse事件
        events.emit(AuthCoreModalEvent.SignResponse, {
          error: error || AuthError.notLogin(),
        });
      }
      setAuthCoreModal({
        particleModalVisible: false,
      });
    };

    window?.particleAuth?.on(AuthCoreEvent.UserInfoChanged, userInfoChanged);
    window?.particleAuth?.on(AuthCoreEvent.ParticleAuthDisconnect, onDisconnect);
    return () => {
      window?.particleAuth?.off(AuthCoreEvent.UserInfoChanged, userInfoChanged);
      window?.particleAuth?.off(AuthCoreEvent.ParticleAuthDisconnect, onDisconnect);
    };
  }, []);

  if (!initParticleAuthRef.current || particleAuth.chains != modalOptions.chains) {
    // authCore 初始化
    particleAuth.init({
      projectId: modalOptions.projectId || props.options.projectId,
      clientKey: modalOptions.clientKey || props.options.clientKey,
      appId: modalOptions.appId || props.options.appId,
      chains: modalOptions.chains,
    });
    const info = getUserInfo();
    setUserInfo(info);
    initParticleAuthRef.current = true;

    if (info) {
      const { token = '', ...restInfo } = info;
      sendAnalyticsActive('open', restInfo);
    }
  }

  useEffect(() => {
    if (isConnected()) {
      syncUserInfo().catch((e) => console.error('syncUserInfo', e));
    }
    loadStyles();

    if (!props?.options?.chains.length) {
      throw new Error('Auth Core: Please set chains in options');
    }
  }, []);

  if (!isServer()) {
    // @ts-ignore
    window.__getConnectCaptcha = getConnectCaptcha;
    // @ts-ignore
    window.__connect = connect;
    // @ts-ignore
    window.__getUserInfo = getUserInfo;
    // @ts-ignore
    window.__getPageHTML = getPageHTML;
    // @ts-ignore
    window.__navigate = customNavigate;
    // @ts-ignore
    window.__particleAuthCoreModal__ = rootRef?.current;
  }

  useEffect(() => {
    console.log('🚀 ~ AuthCoreContextProvider ~ connectionStatus:', connectionStatus);
  }, [connectionStatus]);

  const [socialConnectCallback, setSocialConnectCallback] = useState<SocialConnectCallback>();
  const [walletEntryPlugin, setWalletEntryPlugin] = useState<WalletEntryPlugin>();

  useAsyncEffect(async () => {
    if (modalOptions.wallet !== false) {
      const { walletEntryPlugin } = await import('@particle-network/wallet');
      setWalletEntryPlugin(walletEntryPlugin);
    }
  }, [modalOptions.wallet]);

  return (
    <GlobalContext.Provider
      value={{
        connectionStatus,
        setConnectionStatus,
        userInfo,
        modalOptions,
        setModalOptions,
        showSelectSecurityAccount,
        setWrongPassword,
        setPaymentVerify,
        showAccountTipModal,
        setPaymentPassword,
        loginSuccessRedirectToApp,
        events,
        authCoreModal,
        setAuthCoreModal,
        customRouter,
        setCustomRouter,
        customNavigate,
        setLoginAuthorization,
        authCoreModalClose,
        socialConnectCallback,
        setSocialConnectCallback,
        walletEntryPlugin,
        lang,
        setLang,
      }}
    >
      <ProviderInject>{props.children}</ProviderInject>

      {isServer() ? (
        <div></div>
      ) : (
        <root.div mode='closed'>
          <div
            id='particle-auth-core-modal'
            className={`particle-auth-core-shadow-root ${modalOptions.themeType}`}
            data-path={customRouter?.path}
            data-auth-core-modal-version={getVersion()}
            data-auth-core-version={particleAuth.version}
            ref={rootRef}
            onClick={(event) => {
              event.stopPropagation();
            }}
          >
            {/* <KeyboardProvider container={rootRef.current as HTMLElement}> */}
            <FocusTrap>
              <style data-name='fontStyle'>{fontStyle}</style>
              {!!styles.styleList[0].styleContent &&
                styles.styleList.map((item: any, index: number) => {
                  return (
                    <style data-name={item.name} data-index={index} key={index}>
                      {item.styleContent}
                    </style>
                  );
                })}
              <style data-name='customTheme'>{customThemeStyle as unknown as string}</style>

              {authCoreModal.rootModalContainer && (
                <ParticleModal
                  visible={authCoreModal.particleModalVisible}
                  closable={authCoreModal.closable}
                  onClose={() => {
                    authCoreModalClose();
                  }}
                  className='root-particle-modal-container'
                  modalProps={{
                    footer: null,
                    width: parseInt(modalOptions.customStyle?.modalWidth as unknown as string),
                    height: parseInt(modalOptions.customStyle?.modalHeight as unknown as string),
                    forceRender: true,
                    destroyOnClose: true,
                    style: {
                      maxWidth: modalOptions.customStyle?.modalWidth,
                    },
                    zIndex: modalOptions.customStyle?.zIndex || 99999,
                  }}
                  drawerProps={{
                    height: '86%',
                    closable: false,
                    forceRender: true,
                    destroyOnClose: true,
                    push: false,
                    zIndex: modalOptions.customStyle?.zIndex || 99999,
                  }}
                >
                  <CustomRouter
                    path={customRouter?.path}
                    state={customRouter?.state}
                    replace={customRouter?.replace}
                    children={customRouter?.children}
                  />
                </ParticleModal>
              )}

              <React.Suspense>
                <IframeWalletPlugin />
                <SelectSecurityAccount visible={selectSecurityAccount} state={verifySecurityAccountState} />
                <AccountTipModal modal={accountTipModal} />
                <PaymentVerifyModal props={paymentVerify} />
                <WrongPasswordModal info={wrongPassword} />
              </React.Suspense>
              <AuthCoreModalContainer data-ref='rootModalContainer' setAuthCoreModal={setAuthCoreModal} />
              {props.options.web3Modal && <Web3ModalProvider web3Modal={props.options.web3Modal} />}
            </FocusTrap>
            {/* </KeyboardProvider> */}
          </div>
        </root.div>
      )}
    </GlobalContext.Provider>
  );
};

export const useParticleAuth = () => {
  const context = useContext(GlobalContext);
  if (!context) {
    throw new Error('Auth Core hooks must used in AuthCoreContextProvider');
  }
  return context;
};

export const useAuthCoreModal = () => {
  const { authCoreModal, setAuthCoreModal, authCoreModalClose } = useParticleAuth();
  return {
    authCoreModalClose,
    authCoreModal,
    setAuthCoreModal,
  };
};

export const useCustomRouter = () => {
  const { customRouter, setCustomRouter } = useParticleAuth();
  return {
    customRouter,
    setCustomRouter,
  };
};

export const useCustomNavigate = () => {
  const { customNavigate } = useParticleAuth();

  return customNavigate;
};

export const useSupportedChains = () => {
  const { modalOptions } = useParticleAuth();
  return modalOptions?.chains || [];
};

export const useModalOptions = () => {
  const { modalOptions, setModalOptions } = useParticleAuth();
  return {
    modalOptions,
    setModalOptions,
  };
};

export const useEvents = () => {
  const { events } = useParticleAuth();
  return { events };
};

export const useTranslation = () => {
  const { lang } = useParticleAuth();
  const tempData = (dictionaries[lang.toLowerCase() as keyof typeof dictionaries] || dictionaries.en)();
  type ValueType = Awaited<typeof tempData>;
  const [data, setData] = useState<ValueType>({} as ValueType);

  useEffect(() => {
    async function getTranslations() {
      const targetTranslations = await tempData;

      setData(targetTranslations);
    }

    getTranslations();
  }, [lang]);

  const getTranslation = useCallback(
    (key: string) => {
      const keyItems = key.split('.');
      return get(data, keyItems, '');
    },
    [data]
  );

  return { ...data, t: getTranslation };
};
