import React from 'react';
import { useTranslation } from '../../../context';
import { getNativeSymbol } from '../../../utils/chain-utils';
import { useEVMTransaction } from '../evmContextProvider';
import styles from './info-sign.less';

function NoGas() {
  const { t } = useTranslation();

  const { totalAmountDisplay, currentChain: chainInfo } = useEVMTransaction();

  return (
    <>
      <style>{styles as unknown as string}</style>
      <div className='no-gas-fee'>
        <div className='no-title'>{t('sign.network_fee')}</div>
        <div className='no-warning'>
          <img alt='' src={require('../../../common/images/no_warning.png').default} />
          <span>{t('sign.gas_fee_cannot')}</span>
        </div>
        {totalAmountDisplay && (
          <>
            <div className='data-title'>{t('sign.total')}</div>
            {totalAmountDisplay.amount && (
              <div className='data-item'>
                {totalAmountDisplay.amount} {getNativeSymbol(chainInfo)}
              </div>
            )}
            <div className='data-item'>
              {t('sign.max_amount')}:{' '}
              <span>
                {totalAmountDisplay.maxAmount} {getNativeSymbol(chainInfo)}
              </span>
            </div>
          </>
        )}
      </div>
    </>
  );
}

export default NoGas;
