import { RecordType } from '@particle-network/analytics';
import {
  SolanaEnhancedMethod,
  SolanaRpcMethod,
  analyticsRecord,
  getChainIcon,
  isNeedRestoreWallet,
  particleAuth,
  syncUserInfo,
  type SolanaDeserializeTransactionResult,
  type SplTokenChangeInfo,
  type TokenChangeInfo,
} from '@particle-network/auth-core';
import type { Transaction, VersionedTransaction } from '@solana/web3.js';
import { Button, Modal, Tabs } from 'antd';
import base58 from 'bs58';
import React, { useEffect, useMemo, useState } from 'react';
import CopyToClipboard from 'react-copy-to-clipboard';
import { AuthError } from '../../../api/model/authError';
import { PromptSettingType } from '../../../api/model/bundle';
import { AuthPage } from '../../../components/customRouter';
import IconCopy from '../../../components/icon/icon-copy';
import PowerFooter from '../../../components/power-footer';
import {
  useAuthCoreModal,
  useCustomNavigate,
  useEvents,
  useModalOptions,
  useParticleAuth,
  useTranslation,
} from '../../../context';
import { useError } from '../../../context/hooks/useError';
import useMessage from '../../../context/hooks/useMessage';
import { usePaymentPassword } from '../../../context/hooks/usePaymentPassword';
import { AuthCoreModalEvent } from '../../../context/types';
import { getChainDisplayName } from '../../../utils/chain-utils';
import { shortString } from '../../../utils/common-utils';
import { isTelegramWebApp } from '../../../utils/isTelegramWebApp';
import { formatTokenAmount } from '../../../utils/number-utils';
import solana from '../../../utils/solana-utils';
import styles from './info-sign.less';
import Menu from './menu';

interface IProps {
  param: unknown;
  method: SolanaRpcMethod;
  chainId?: number;
  loginAuthorizationSign?: boolean;
}
function InfoSign(props: IProps) {
  const { method, param, chainId, loginAuthorizationSign } = props;
  const { t } = useTranslation();
  const navigate = useCustomNavigate();
  const { setPaymentVerify, userInfo, setPaymentPassword } = useParticleAuth();
  const { events } = useEvents();
  const { modalOptions } = useModalOptions();
  const { authCoreModal } = useAuthCoreModal();
  const { errorHandle } = useError();

  const [loading, setLoading] = useState(false);
  const message = useMessage();
  const [transactionInfo, setTransactionInfo] = useState<SolanaDeserializeTransactionResult>();

  const [nftChanges, setNftChanges] = useState<TokenChangeInfo[]>();

  const [tokenChanges, setTokenChanges] = useState<SplTokenChangeInfo[]>();

  const [headerTitle, setHeaderTitle] = useState<string>('');
  const [headerDes, setHeaderDes] = useState<string>('');

  const { hasSetPaymentPassword, showSetPaymentPasswordOrConfirm } = usePaymentPassword();

  const chain = useMemo(() => {
    return {
      id: chainId || particleAuth.solana.chainId,
      name: 'Solana',
    };
  }, []);

  const shortAddress = useMemo(() => {
    const address = particleAuth.solana.selectedAddress as string;
    return shortString(address);
  }, []);

  useEffect(() => {
    syncUserInfo().catch((e) => console.log('syncUserInfo', e));
    if (isNeedRestoreWallet()) {
      navigate(AuthPage.MasterPasswordVerify);
    }
  }, [navigate]);

  const loadsecurityAccounts = () => {
    syncUserInfo()
      .then(() => {
        approveSign();
      })
      .catch((error) => {
        setLoading(false);
        message.error(error.message ?? 'Sign Error');
      });
  };

  const finallyHandle = (result: any, error?: any) => {
    events.emit(AuthCoreModalEvent.SignResponse, {
      result,
      error,
    });
  };

  async function asyncFilter<T>(predicate: (indo: T) => Promise<boolean>, arr?: T[]): Promise<T[]> {
    if (!arr) {
      return [];
    }
    const results = await Promise.all(arr.map(predicate));
    return arr.filter((_v, index) => results[index]);
  }

  const approveSign = async () => {
    if (!method) return;

    analyticsRecord({
      // eslint-disable-next-line camelcase
      record_type: RecordType.PAGE_SIGN_CONFIRM_BUTTON_CLICK, // confirm按钮点击
    });

    if (userInfo?.security_account?.has_set_payment_password) {
      setPaymentVerify({
        visible: true,
        onVerifyCompleted: approveSignImpl,
      });
    } else if (
      method === SolanaRpcMethod.signAndSendTransaction ||
      method === SolanaRpcMethod.signTransaction ||
      method === SolanaRpcMethod.signAllTransactions ||
      modalOptions?.promptSettingConfig?.promptPaymentPasswordSettingWhenSign === PromptSettingType.everyAndNotSkip
    ) {
      // 显示设置支付密码
      showSetPaymentPasswordOrConfirm(approveSignImpl);
    } else {
      approveSignImpl();
    }
  };

  const approveSignImpl = async () => {
    if (!method) return;
    setLoading(true);
    let signed;
    try {
      if (method === SolanaRpcMethod.signMessage) {
        const { signature } = await particleAuth.solana.signMessage(param as Uint8Array);
        signed = signature;
      } else if (method === SolanaRpcMethod.signAndSendTransaction) {
        const { signature } = await particleAuth.solana.signAndSendTransaction(
          param as Transaction | VersionedTransaction,
          chain.id
        );
        signed = signature;
      } else if (method === SolanaRpcMethod.signTransaction) {
        signed = await particleAuth.solana.signTransaction(param as Transaction | VersionedTransaction, chain.id);
      } else if (method === SolanaRpcMethod.signAllTransactions) {
        signed = await particleAuth.solana.signAllTransactions(
          param as (Transaction | VersionedTransaction)[],
          chain.id
        );
      } else {
        throw new Error('Unknown method');
      }

      analyticsRecord({
        // eslint-disable-next-line camelcase
        record_type: RecordType.PAGE_SIGN_CONFIRM_BUTTON_CLICK_SUCCESS, // confirm成功
      });
    } catch (e: any) {
      console.error('approveSignImpl error', method, e);
      analyticsRecord({
        // eslint-disable-next-line camelcase
        record_type: RecordType.PAGE_SIGN_CONFIRM_BUTTON_CLICK_FAILURE, // confirm失败
      });

      if (e?.error_code === 50103 && !userInfo?.security_account?.has_set_payment_password) {
        loadsecurityAccounts();
      } else if (e?.message === 'Local Key not found' || e?.message === 'Master password decryption error') {
        navigate(AuthPage.MasterPasswordVerify);
      } else {
        errorHandle(e);
      }
    } finally {
      setLoading(false);
    }

    if (signed) {
      if (method === SolanaRpcMethod.signMessage && loginAuthorizationSign) {
        events.emit(AuthCoreModalEvent.LoginSuccess, {
          ...userInfo,
          authorization: {
            message: base58.encode(param as Uint8Array),
            signature: signed,
          },
        });
      } else {
        finallyHandle(signed);
      }
    }
  };

  const cancelSign = () => {
    if (loading) {
      return;
    }

    if (loginAuthorizationSign) {
      events.emit(AuthCoreModalEvent.LoginSuccess, userInfo);
    } else {
      finallyHandle(undefined, AuthError.userRejectedRequest());
    }
  };

  useEffect(() => {
    if (method === SolanaRpcMethod.signMessage) {
      setHeaderTitle(t('sign.signature_message') as string);
      setHeaderDes(t('sign.signature_title') as string);
    } else if (method === SolanaRpcMethod.signAndSendTransaction) {
      setHeaderTitle(t('sign.send_transaction') as string);
      setHeaderDes(t('sign.approve_and').format(getChainDisplayName(chain)));
      const serializeTx = (param as Transaction | VersionedTransaction).serialize({
        requireAllSignatures: false,
        verifySignatures: false,
      });
      deserializeTransaction([base58.encode(serializeTx)]);
    } else if (method === SolanaRpcMethod.signTransaction) {
      setHeaderTitle(t('sign.sign_transaction') as string);
      setHeaderDes(t('sign.sign_but') as string);
      const serializeTx = (param as Transaction | VersionedTransaction).serialize({
        requireAllSignatures: false,
        verifySignatures: false,
      });
      deserializeTransaction([base58.encode(serializeTx)]);
    } else if (method === SolanaRpcMethod.signAllTransactions) {
      setHeaderTitle(t('sign.sign_transaction') as string);
      setHeaderDes(t('sign.sign_but') as string);
      const serializeTxs = (param as (Transaction | VersionedTransaction)[]).map((tx) =>
        base58.encode(
          tx.serialize({
            requireAllSignatures: false,
            verifySignatures: false,
          })
        )
      );
      deserializeTransaction(serializeTxs);
    } else {
      throw new Error('Unknown method');
    }
  }, [param, t]);

  useEffect(() => {
    // init connect();
    particleAuth.solana.connect();
  }, []);

  const deserializeTransaction = (messages: string[]) => {
    particleAuth.solana
      .request({
        chainId: chain.id,
        method: SolanaEnhancedMethod.enhancedDeserializeTransaction,
        params: messages,
      })
      .then((result) => {
        setTransactionInfo(result);
        asyncFilter(async (info: any) => {
          const associatedTokenAddress = await solana.findAssociatedTokenAddress(
            particleAuth.solana.selectedAddress as string,
            info.mint
          );
          return info.associatedTokenAddress === associatedTokenAddress.toBase58();
        }, result?.estimatedChanges?.nfts).then((results) => {
          console.log('NFTChanges', results.length);
          setNftChanges(results);
        });

        asyncFilter(async (info: any) => {
          const associatedTokenAddress = await solana.findAssociatedTokenAddress(
            particleAuth.solana.selectedAddress as string,
            info.mint
          );
          return info.associatedTokenAddress === associatedTokenAddress.toBase58();
        }, result?.estimatedChanges?.tokens).then((results) => {
          console.log('TokenChanges', results.length);
          setTokenChanges(results);
        });
      })
      .catch((error) => {
        console.log('deserializeTransaction error', error);
        Modal.error({
          title: error.message ?? 'Deserialize Transaction Error',
          okCancel: true,
          cancelText: t('common.cancel'),
          okText: t('common.retry'),
          wrapClassName: 'auth-core-modal-error',
          getContainer: () => {
            return authCoreModal.rootBody as HTMLElement;
          },
          onOk: () => {
            deserializeTransaction(messages);
          },
        });
      });
  };

  const signMessage = () => {
    const textDecoder = new TextDecoder();
    const decodedString = textDecoder.decode(param as any);
    return (
      <div className='sign-message'>
        <div className={'message' + (hasSetPaymentPassword ? '' : ' no-password-tip')}>
          <div className='pre-wrap personal-message'>{decodedString}</div>
        </div>
      </div>
    );
  };

  const signTransaction = () => {
    return (
      <Tabs
        defaultActiveKey='1'
        items={[
          {
            label: t('sign.details'),
            key: '1',
            children: (
              <>
                <div className='balance-change'>
                  <div className='title'>{t('sign.estimated_balance_change')}</div>
                  <div className='change-body'>
                    {transactionInfo?.estimatedChanges?.sols
                      .filter((info) => info.address === (particleAuth?.solana?.selectedAddress as string))
                      .map((info, index) => {
                        return (
                          <div className='change-title' key={`sol-change-${index}`}>
                            SOL
                            <div className='change-val' style={info.lamportsChange < 0 ? { color: '#ea4335' } : {}}>
                              {info.lamportsChange < 0 ? '' : '+'}
                              {formatTokenAmount(info.lamportsChange, 9)}
                            </div>
                          </div>
                        );
                      })}

                    {nftChanges?.map((info, index) => {
                      return (
                        <div className='change-title' key={`nft-change-${index}`}>
                          {info.name ? info.name : 'Unknown NFT'}
                          <div className='change-val' style={info.amountChange < 0 ? { color: '#ea4335' } : {}}>
                            {info.amountChange < 0 ? '' : '+'}
                            {info.amountChange}
                          </div>
                        </div>
                      );
                    })}

                    {tokenChanges?.map((info, index) => {
                      return (
                        <div className='change-title' key={`token-change-${index}`}>
                          {info.name ? info.name : 'Unknown Token'}
                          <div className='change-val' style={info.amountChange < 0 ? { color: '#ea4335' } : {}}>
                            {info.amountChange < 0 ? '' : '+'}
                            {formatTokenAmount(info.amountChange, info.decimals)}
                          </div>
                        </div>
                      );
                    })}
                  </div>
                </div>

                <div className='net-fee solana'>
                  <div className='title'>
                    {t('sign.network_fee')}
                    {transactionInfo && (
                      <div className='fee-val'>{formatTokenAmount(transactionInfo.estimatedLamportsFee, 9)} SOL</div>
                    )}
                  </div>
                </div>
              </>
            ),
          },
          {
            label: t('sign.data'),
            key: '2',
            children: (
              <div>
                {transactionInfo?.instructions.map((instruction, index) => {
                  return (
                    <div className='inner-instruction' key={`instruction-${index}`}>
                      <div className='inner-content'>
                        <div className='content-item'>
                          <div className='item'>
                            <div className='item-0'>
                              #{index + 1} - {t(`program.${instruction.type}`)}
                            </div>
                            <div className='item-1 mt10'>
                              {t('sign.program_id')}
                              <span>{shortString(instruction.programId)}</span>
                            </div>
                            <div className='item-1 mt15'>
                              {t('sign.data')}
                              <span>{shortString(instruction.data)}</span>
                            </div>
                          </div>
                        </div>
                      </div>
                    </div>
                  );
                })}
              </div>
            ),
          },
        ]}
      />
    );
  };

  return (
    <div className='info-sign'>
      <style>{styles as unknown as string}</style>
      {!hasSetPaymentPassword && (
        <div className='has-payment-password' data-telegram={isTelegramWebApp()}>
          <div className='has-payment-password-icon'></div>
          <div className='has-payment-password-tip'>{t('account.waring_tip1')}</div>
          <div className='has-payment-password-set' onClick={setPaymentPassword}>
            {t('account.set')}
          </div>
        </div>
      )}
      <div className={'scroll-part' + (hasSetPaymentPassword ? '' : ' no-password-tip')}>
        <Menu userInfo={userInfo} transactionInfo={transactionInfo} />
        <div className='info-request'>{headerTitle}</div>
        <div className='info-title'>
          <img src={getChainIcon(chain)} alt='' />
          {getChainDisplayName(chain)}
        </div>
        <CopyToClipboard
          text={particleAuth.solana.selectedAddress as string}
          onCopy={() => message.success(t('new.copied_to'))}
        >
          <div className='info-address'>
            {shortAddress}
            <div className='copy-icon'>
              <IconCopy />
            </div>
          </div>
        </CopyToClipboard>
        <div className='info-des'>{headerDes}</div>
        <div className='apart-line'></div>
        {method === SolanaRpcMethod.signMessage && signMessage()}
        {method !== SolanaRpcMethod.signMessage && signTransaction()}
      </div>

      <div className='btn-box'>
        <div>
          <Button className='btn-cancel' onClick={cancelSign}>
            {t('common.cancel')}
          </Button>
          <Button className='btn-approve' onClick={approveSign} loading={loading}>
            {t('common.confirm')}
          </Button>
        </div>
        <PowerFooter></PowerFooter>
      </div>
    </div>
  );
}

export default InfoSign;
