import Icon from '@ant-design/icons';
import React from 'react';
import styles from './index.less';

const CircleClose = ({ className = '', ...others }) => {
  const svg = require('../../common/icon/circle_close.svg').default;
  return (
    <>
      <style>{styles as unknown as string}</style>
      <Icon
        className={className ? `circle-close-icon ${className}` : 'circle-close-icon'}
        //@ts-ignore
        component={svg}
        {...others}
      />
    </>
  );
};

export default CircleClose;
