import { isHexString } from '@ethereumjs/util';
import type { Chain } from '@particle-network/auth-core';
import {
  defaultTokenIcon,
  getChainIcon,
  getChainInfo,
  getEVMChainInfoById,
  getSolanaChainInfoById,
  tronAddressFromHex,
} from '@particle-network/auth-core';
import type { Chain as ViemChain } from 'viem/chains';

export const getChainDisplayName = (chain: ViemChain | Chain) => {
  const chainInfo = getChainInfo(chain);
  if (!chainInfo) {
    return '';
  }
  let cnahnIname = '';
  if (!chainInfo.testnet && !chainInfo.name.toLowerCase().includes('mainnet')) {
    cnahnIname = chainInfo.name + ' Mainnet';
  } else {
    cnahnIname = chainInfo.name;
  }
  return cnahnIname.replace('BNB Smart Chain', 'BNB Chain').replace('Binance Smart Chain', 'BNB Chain');
};

export function getNativeSymbol(chain: Chain): string {
  return (getChainInfo(chain)?.nativeCurrency?.symbol || '').replace('tBNB', 'BNB');
}

export function getEvmChain(id: number) {
  return getEVMChainInfoById(id);
}

export function getSolanaChain(id: number) {
  return getSolanaChainInfoById(id);
}

export function formatAddress(address: string, chain: Chain): string {
  if (isTron(chain) && isHexString(address)) {
    return tronAddressFromHex(address);
  }
  return address;
}

export function isSolana(chain: Chain) {
  return chain.name.toLowerCase() === 'solana';
}

export function isEvm(chain: Chain) {
  return chain.name.toLowerCase() !== 'solana';
}

export function isTron(chain?: Chain) {
  return chain && chain.name.toLowerCase() === 'tron';
}

export const formatPng = (src = '') => {
  if (!src) return src;
  return src + (src.includes('?') ? '&' : '?') + 'x-oss-process=image/format,png';
};

export function getNativeTokenIcon(chain?: Chain): string {
  if (chain) {
    const chainInfo = getChainInfo(chain);
    if (chainInfo) {
      const symbol = chainInfo?.nativeCurrency.symbol?.toLowerCase() || '';
      if (['bnb', 'btc', 'eth', 'stbtc', 'usdc', 'usdt'].includes(symbol)) {
        return `https://static.particle.network/chains/evm/symbol/${symbol}.png`;
      } else {
        return getChainIcon(chainInfo);
      }
    } else {
      return defaultTokenIcon;
    }
  }
  return defaultTokenIcon;
}
