import { isNeedRestoreWallet, syncUserInfo } from '@particle-network/auth-core';
import React, { useEffect } from 'react';
import { AuthPage } from '../../components/customRouter';
import { useCustomNavigate } from '../../context';
import EvmSign from './components/evm-sign';
import { EVMContextProvider } from './evmContextProvider';

export type EvmSignProps = {
  method: string;
  param: unknown;
  loginAuthorizationSign?: boolean;
};

function Sign(props: EvmSignProps) {
  const navigate = useCustomNavigate();

  useEffect(() => {
    syncUserInfo().catch((e) => console.log('syncUserInfo', e));
    if (isNeedRestoreWallet()) {
      navigate(AuthPage.MasterPasswordVerify);
    }
  }, []);

  return (
    <EVMContextProvider {...props}>
      <EvmSign {...props} />
    </EVMContextProvider>
  );
}

export default Sign;
