import { isHexString } from '@ethereumjs/util';
import type { Chain, GasFeeMode } from '@particle-network/auth-core';
import {
  isChainSupportEIP1559,
  tronAddressToHex,
  type EIP1559TxData,
  type EVMTransaction,
  type TxData,
} from '@particle-network/auth-core';
import { BigNumber } from 'bignumber.js';
import type { Chain as ViemChain } from 'viem/chains';
import { toHexPrefixString } from './common-utils';
export function isEIP1559Type(type: string | number | undefined): boolean {
  if (typeof type == 'number') {
    return type === 2;
  } else if (typeof type == 'string') {
    return Number(type) === 2;
  }
  return false;
}

const parseValue = (value: string | number) => {
  if (typeof value === 'string') {
    if (value.startsWith('0x')) {
      return value;
    } else {
      return `0x${new BigNumber(value).toString(16)}`;
    }
  } else {
    return toHexPrefixString(value);
  }
};

export function parseTransaction(txData: TxData | EIP1559TxData, chainInfo: ViemChain): EVMTransaction {
  let nonce,
    gasPrice,
    gasLimit,
    from,
    to,
    value,
    data,
    type,
    chainId,
    maxPriorityFeePerGas,
    maxFeePerGas,
    action,
    gasLevel;

  const tron = isTron(chainInfo);
  if (tron && !isHexString(txData.from)) {
    from = tronAddressToHex(txData.from);
  } else {
    from = txData.from;
  }
  if (txData.to) {
    if (tron && !isHexString(txData.to)) {
      to = tronAddressToHex(txData.to);
    } else {
      to = txData.to;
    }
  }

  if (txData.gas && !txData.gasLimit) {
    gasLimit = parseValue(txData.gas);
  } else if (txData.gasLimit) {
    gasLimit = parseValue(txData.gasLimit);
  }
  if (txData.value) {
    value = parseValue(txData.value);
  }
  if (txData.data) {
    data = toHexPrefixString(txData.data);
  }
  if (txData.chainId) {
    chainId = parseValue(txData.chainId);
  } else {
    chainId = parseValue(chainInfo.id);
  }
  if (txData.nonce) {
    nonce = parseValue(txData.nonce);
  }
  if ('maxPriorityFeePerGas' in txData && txData.maxPriorityFeePerGas) {
    maxPriorityFeePerGas = parseValue(txData.maxPriorityFeePerGas);
  }
  if ('maxFeePerGas' in txData && txData.maxFeePerGas) {
    maxFeePerGas = parseValue(txData.maxFeePerGas);
  }
  if (txData.gasPrice) {
    gasPrice = parseValue(txData.gasPrice);
  }

  if (txData.type || txData.type === 0) {
    type = parseValue(txData.type);
  } else {
    if (
      'maxPriorityFeePerGas' in txData &&
      txData.maxPriorityFeePerGas &&
      'maxFeePerGas' in txData &&
      txData.maxFeePerGas &&
      isChainSupportEIP1559(txData)
    ) {
      type = '0x2';
    } else if ('gasPrice' in txData && txData.gasPrice) {
      type = '0x0';
    } else {
      type = isChainSupportEIP1559(txData) ? '0x2' : '0x0';
    }
  }

  if (txData.action && ['normal', 'cancel', 'speedup'].includes(txData.action)) {
    action = txData.action;
  } else {
    action = undefined;
  }

  if (txData.gasLevel && ['custom', 'low', 'medium', 'high'].includes(txData.gasLevel)) {
    gasLevel = txData.gasLevel as GasFeeMode;
  } else {
    gasLevel = undefined;
  }

  return {
    nonce,
    gasPrice,
    gasLimit,
    from,
    to,
    value,
    data,
    type,
    chainId,
    maxPriorityFeePerGas,
    maxFeePerGas,
    action,
    gasLevel,
  };
}

export function isEMVMethod(method: string): boolean {
  return typeof method === 'string' && (method.startsWith('eth_') || method.startsWith('personal_'));
}

/**
 * isEVMAddress
 * @param address
 * @returns
 */
export function isEVMAddress(address: string): boolean {
  // @ts-ignore
  return address && address.length === 42 && address.startsWith('0x');
}

export function isTron(chain: Chain): boolean {
  return chain.name.toLowerCase() === 'tron';
}
