import {
  AuthCoreEvent,
  AuthType,
  check,
  disconnect,
  getCloudflareTurnstileResponse,
  getConnectCaptcha,
  isConnected,
  isNullish,
  particleAuth,
  requireNotNullish,
  type CloudflareOptions,
  type ConnectParam,
  type E164Phone,
  type UserInfo,
} from '@particle-network/auth-core';
import { useCallback, useEffect, useMemo } from 'react';
import { AuthPage } from '../../components/customRouter';
import { useCustomNavigate, useEvents, useParticleAuth } from '../../context';
import { isValidCaptcha, isValidE164PhoneNumber, isValidEmail } from '../../utils';
import { AuthCoreModalEvent } from '../types';
import useLogin from './useLogin';

export const useConnect = () => {
  const navigate = useCustomNavigate();
  const { events } = useEvents();
  const {
    modalOptions,
    setLoginAuthorization,
    setConnectionStatus,
    connectionStatus,
    setAuthCoreModal,
    socialConnectCallback,
    setSocialConnectCallback,
  } = useParticleAuth();
  const { loginHandler, socialAuthLogin } = useLogin();

  const isLoginDirectly = useCallback((options: ConnectParam) => {
    // @ts-ignore
    const { email, phone, code, provider, thirdpartyCode, socialType } = options;

    return (
      ((isValidEmail(email) || isValidE164PhoneNumber(phone)) && isValidCaptcha(code)) ||
      socialType ||
      (provider && thirdpartyCode)
    );
  }, []);

  const connect = useCallback(
    async (options?: ConnectParam): Promise<UserInfo | undefined> => {
      if (options && options.authorization) {
        check(options.chain, 'chain is required when authorization is provided');
      }
      if (options && modalOptions.authTypes && modalOptions.authTypes.length > 0) {
        if ('email' in options && !isNullish(options.email)) {
          check(
            modalOptions.authTypes.includes(AuthType.email),
            'Email login is not supported, please add AuthType.email to AuthCoreContextProvider init-param "authTypes"'
          );
        } else if ('phone' in options && !isNullish(options.phone)) {
          check(
            modalOptions.authTypes.includes(AuthType.phone),
            'Phone login is not supported, please add AuthType.phone to AuthCoreContextProvider init-param "authTypes"'
          );
        } else if ('socialType' in options && options.socialType) {
          const type = (options.socialType as string) === 'twitterv1' ? 'twitter' : options.socialType;
          check(
            modalOptions.authTypes.includes(type as AuthType),
            `${type} login is not supported, please add AuthType.${type} to AuthCoreContextProvider init-param "authTypes"`
          );
        } else if ('provider' in options && options.provider && 'thirdpartyCode' in options && options.thirdpartyCode) {
          check(
            modalOptions.authTypes.includes(options.provider),
            `${options.provider} login is not supported, please add ${options.provider} to AuthCoreContextProvider init-param "authTypes"`
          );
        }
      }
      setConnectionStatus('connecting');
      return new Promise((resolve, reject) => {
        events.once(AuthCoreModalEvent.LoginSuccess, (userInfo) => {
          setConnectionStatus('connected');
          // 关闭窗口
          setAuthCoreModal({
            particleModalVisible: false,
          });
          const { socialType, nonce, code } = (options as any) || {};
          if (socialType && nonce && code) {
            socialConnectCallback?.onSuccess?.(userInfo);
          }
          resolve(userInfo);
        });

        events.once(AuthCoreModalEvent.LoginFail, (error) => {
          setConnectionStatus('disconnected');
          setAuthCoreModal({
            particleModalVisible: false,
          });
          const { socialType, nonce, code } = (options as any) || {};
          if (socialType && nonce && code) {
            socialConnectCallback?.onError?.(error);
          }
          reject(error);
        });
        if (options && isLoginDirectly(options)) {
          // social login, jwt, telegram, phone with code, email with code.
          const { authorization, chain, socialType, nonce, prompt, code } = options as any;
          if (authorization) {
            setLoginAuthorization({ authorization, chain: requireNotNullish(chain) });
          } else {
            setLoginAuthorization(undefined);
          }
          if (socialType && (!nonce || !code)) {
            socialAuthLogin({
              socialType,
              prompt,
              authorization,
              chain,
            });
          } else {
            loginHandler(options as ConnectParam);
          }
        } else {
          navigate(AuthPage.Login, {
            state: options,
          });
        }
      });
    },
    [events, modalOptions.authTypes, loginHandler, isLoginDirectly, navigate, socialConnectCallback]
  );

  useEffect(() => {
    const onConnect = () => {
      setConnectionStatus('connected');
    };

    const onDisconnect = () => {
      setConnectionStatus('disconnected');
    };
    particleAuth.on(AuthCoreEvent.ParticleAuthConnect, onConnect);
    particleAuth.on(AuthCoreEvent.ParticleAuthDisconnect, onDisconnect);

    if (isConnected()) {
      setConnectionStatus('connected');
    }

    return () => {
      particleAuth.off(AuthCoreEvent.ParticleAuthConnect, onConnect);
      particleAuth.off(AuthCoreEvent.ParticleAuthDisconnect, onDisconnect);
    };
  }, []);

  const requestConnectCaptcha = useCallback(
    async (
      config: { email: string } | { phone: E164Phone; cloudflareOptions?: CloudflareOptions }
    ): Promise<boolean> => {
      if ('email' in config && config.email) {
        return await getConnectCaptcha(config);
      } else if ('phone' in config && config.phone) {
        const token = await getCloudflareTurnstileResponse(config.cloudflareOptions);
        // eslint-disable-next-line camelcase
        return await getConnectCaptcha({ phone: config.phone, cf_turnstile_response: token });
      }
      throw new Error('Invalid config');
    },
    []
  );

  const connected = useMemo(() => connectionStatus === 'connected', [connectionStatus]);

  return {
    connect,
    disconnect,
    connectionStatus,
    connected,
    requestConnectCaptcha,
    setSocialConnectCallback,
  };
};
