import type { CountryCode } from 'libphonenumber-js/max';
import { isValidPhoneNumber, parsePhoneNumberWithError } from 'libphonenumber-js/max';
import darkApple from '../common/images/apple_dark_icon.png';
import apple from '../common/images/apple_icon.png';
import discord from '../common/images/discord_icon.png';
import email from '../common/images/email_icon.png';
import facebook from '../common/images/facebook_icon.png';
import darkGithub from '../common/images/github_dark_icon.png';
import github from '../common/images/github_icon.png';
import google from '../common/images/google_icon.png';
import jwt from '../common/images/jwt_icon.png';
import linkedin from '../common/images/linkedin_icon.png';
import microsoft from '../common/images/microsoft_icon.png';
import passkeys from '../common/images/passkeys_icon.png';
import phone from '../common/images/phone_icon.png';
import twitch from '../common/images/twitch_icon.png';
import darkTwitter from '../common/images/twitter_dark_icon.png';
import twitter from '../common/images/twitter_icon.png';
import type { Theme } from '../types';

export const ipfsToSrc = (ipfs: string) => {
  if (!ipfs || !ipfs.startsWith('ipfs://')) {
    return ipfs || '';
  }

  return `https://ipfs.io/ipfs/${encodeURI(ipfs.slice(7))}`;
};

export const EmailRegExp = /^\w+([-+.]\w+)*@[a-zA-Z0-9]+([.-][a-zA-Z0-9]+)*\.[a-zA-Z0-9]+([.-][a-zA-Z0-9]+)*$/;

export const PhoneRegExp = /^[0-9]{4,16}$/;

export const PhoneE164Exp = /^\+[1-9]\d{4,14}$/;

export function isValidEmail(email?: string) {
  if (email && EmailRegExp.test(email)) {
    return email;
  }
}

export function isPhoneValid(phone: string, regionCode: string): boolean {
  if (!phone || phone.length < 5) {
    return false;
  }
  const countryCode = regionCode?.toUpperCase() as CountryCode;
  const result = isValidPhoneNumber(phone, countryCode);
  return result;
}

export const isValidE164PhoneNumber = (phone: string | undefined) => {
  if (phone && PhoneE164Exp.test(phone)) {
    try {
      if (isValidPhoneNumber(phone)) {
        const e164Phone = parsePhoneNumberWithError(phone).format('E.164');
        return e164Phone;
      }
    } catch (e) {
      // ignore
    }
  }
};

export function isValidCaptcha(code?: string) {
  return code && code.match(/^\d{6}$/);
}

export const isAutoFocusSupported = () => {
  return true;
};

export const isMobile = () => {
  if (isServer()) return false;
  return /Android|webOS|iPhone|iPad|iPod|BlackBerry|IEMobile|Opera Mini/i.test(navigator.userAgent);
};

export const isServer = () => typeof window === 'undefined';

export const isClient = () => typeof window !== 'undefined';

export const getLoginLogos = (theme?: Theme): Record<string, string> => {
  const twitterIcon = theme === 'dark' ? darkTwitter : twitter;
  const githubIcon = theme === 'dark' ? darkGithub : github;
  const appleIcon = theme === 'dark' ? darkApple : apple;
  return {
    email,
    phone,
    google,
    facebook,
    twitter: twitterIcon,
    linkedin,
    github: githubIcon,
    microsoft,
    apple: appleIcon,
    discord,
    twitch,
    jwt,
    passkeys,
  };
};
