import React, { useMemo } from 'react';
import { useCustomize } from '../context/hooks';
import styles from './power-footer.less';

function PowerFooter(props: any) {
  const { themeType } = useCustomize();
  const imgUrl = useMemo(() => {
    return themeType === 'dark'
      ? require('../common/images/footer_img_b.png').default
      : require('../common/images/footer_img.png').default;
  }, [themeType]);

  return (
    <>
      <style>{styles as unknown as string}</style>
      <div className='footer-box' {...props}>
        <img className='footer' src={imgUrl} alt='' />
      </div>
    </>
  );
}

export default PowerFooter;
