import { hasPaymentPassword, isNullish } from '@particle-network/auth-core';
import { useEffect, useState } from 'react';
import { useParticleAuth } from '..';
import { PromptSettingType } from '../../api/model/bundle';
import { PreferenceKey, load, save } from '../../repository';

export const usePaymentPassword = () => {
  const [hasSetPaymentPassword, setHasSetPaymentPassword] = useState(hasPaymentPassword());

  const { setPaymentPassword, userInfo, modalOptions, showAccountTipModal } = useParticleAuth();

  useEffect(() => {
    setHasSetPaymentPassword(Boolean(userInfo?.security_account?.has_set_payment_password));
  }, [userInfo]);

  const showSetPaymentPasswordOrConfirm = (confirm: () => void) => {
    if (hasSetPaymentPassword) {
      confirm();
    } else {
      if (
        modalOptions.promptSettingConfig?.promptPaymentPasswordSettingWhenSign === PromptSettingType.every ||
        modalOptions.promptSettingConfig?.promptPaymentPasswordSettingWhenSign === PromptSettingType.everyAndNotSkip
      ) {
        showAccountTipModal({
          visible: true,
          confirm,
        });
      } else if (
        (modalOptions.promptSettingConfig?.promptPaymentPasswordSettingWhenSign ||
          isNullish(modalOptions.promptSettingConfig?.promptPaymentPasswordSettingWhenSign)) &&
        !load(PreferenceKey.PN_OPEN_SET_PAYMENT_PASSWORD)
      ) {
        save(PreferenceKey.PN_OPEN_SET_PAYMENT_PASSWORD, '1');
        showAccountTipModal({
          visible: true,
          confirm,
        });
      } else {
        confirm();
      }
    }
  };

  return {
    hasSetPaymentPassword,
    setPaymentPassword,
    showSetPaymentPasswordOrConfirm,
  };
};
