import {
  EvmRpcMethod,
  SignTypedDataVersion,
  type GasFee,
  type PrefixedHexString,
  type TotalAmount,
} from '@particle-network/auth-core';
import { BigNumber } from 'bignumber.js';
import { fromWeiFormat } from './number-utils';

export const ethereumUtils = {
  gasFee({
    gasLimit,
    baseFee,
    maxFeePerGas,
    maxPriorityFeePerGas,
    gasPrice,
  }: {
    gasLimit: PrefixedHexString | number;
    baseFee: PrefixedHexString | number;
    maxFeePerGas?: PrefixedHexString | number;
    maxPriorityFeePerGas?: PrefixedHexString | number;
    gasPrice?: PrefixedHexString | number;
  }): GasFee {
    if (maxFeePerGas && maxPriorityFeePerGas && baseFee && gasLimit) {
      //eip 1559
      const fee = new BigNumber(maxPriorityFeePerGas)
        .plus(new BigNumber(baseFee))
        .multipliedBy(new BigNumber(gasLimit));

      const maxFee = new BigNumber(maxFeePerGas).plus(new BigNumber(baseFee)).multipliedBy(new BigNumber(gasLimit));
      return {
        fee: fromWeiFormat(fee.toFixed()),
        maxFee: fromWeiFormat(maxFee.toFixed()),
      };
    } else if (gasPrice) {
      const fee = new BigNumber(gasPrice).multipliedBy(new BigNumber(gasLimit));
      return {
        maxFee: fromWeiFormat(fee.toFixed()),
      };
    }
    return {
      maxFee: '',
    };
  },

  totalAmount({
    value,
    gasLimit,
    baseFee,
    maxFeePerGas,
    maxPriorityFeePerGas,
    gasPrice,
  }: {
    value?: PrefixedHexString | number;
    gasLimit: PrefixedHexString | number;
    baseFee: PrefixedHexString | number;
    maxFeePerGas?: PrefixedHexString | number;
    maxPriorityFeePerGas?: PrefixedHexString | number;
    gasPrice?: PrefixedHexString | number;
  }): TotalAmount {
    if (!value) {
      value = '0x0';
    }
    if (maxFeePerGas && maxPriorityFeePerGas) {
      //eip 1559
      const fee = new BigNumber(maxPriorityFeePerGas)
        .plus(new BigNumber(baseFee))
        .multipliedBy(new BigNumber(gasLimit));

      const maxFee = new BigNumber(maxFeePerGas).plus(new BigNumber(baseFee)).multipliedBy(new BigNumber(gasLimit));
      return {
        amount: fromWeiFormat(new BigNumber(value).plus(fee).toString()),
        maxAmount: fromWeiFormat(new BigNumber(value).plus(maxFee).toString()),
      };
    } else if (gasPrice) {
      const fee = new BigNumber(gasPrice).multipliedBy(new BigNumber(gasLimit));
      return {
        maxAmount: fromWeiFormat(new BigNumber(value).plus(fee).toString()),
      };
    }
    return {
      maxAmount: '',
    };
  },

  getSignTypedVersion(method: string): SignTypedDataVersion {
    if (method === EvmRpcMethod.ethSignTypedData || method === EvmRpcMethod.ethSignTypedDataV1) {
      return SignTypedDataVersion.V1;
    } else if (method === EvmRpcMethod.ethSignTypedDataV3) {
      return SignTypedDataVersion.V3;
    } else {
      return SignTypedDataVersion.V4;
    }
  },

  isSignTypedDataUniq(method: string): boolean {
    return method.includes('uniq');
  },
};
