import {
  EvmEnhancedMethod,
  GasFeeMode,
  getEVMChainInfoById,
  isNullish,
  particleAuth,
  type EIP1559TxData,
  type GasFeeResult,
  type TxData,
} from '@particle-network/auth-core';
import { bnToHex } from './common-utils';
import { toWei } from './number-utils';
import { parseTransaction } from './transaction-utils';

const evmSendTransaction = async (transaction: Omit<TxData | EIP1559TxData, 'from'>): Promise<string> => {
  const chainId = Number(transaction.chainId || particleAuth.ethereum.chainId);
  const txData = {
    ...transaction,
    from: particleAuth.ethereum.selectedAddress!,
    chainId,
  };

  const chain = getEVMChainInfoById(chainId)!;

  if (isNullish(txData.data)) {
    txData.data = '0x';
  }

  const evmTx = parseTransaction(txData, chain);

  if (!evmTx.gasLimit) {
    evmTx.gasLimit = await fetchEstimateGas(
      { from: evmTx.from, to: evmTx.to, value: evmTx.value, data: evmTx.data },
      chainId
    );
  }

  if (Number(evmTx.type) === 2) {
    if (!evmTx.maxPriorityFeePerGas || !evmTx.maxFeePerGas) {
      const gasFee = await fetchSuggestedGasFee(chainId);
      evmTx.maxFeePerGas = bnToHex(toWei(gasFee[GasFeeMode.medium].maxFeePerGas, 'gwei'));
      evmTx.maxPriorityFeePerGas = bnToHex(toWei(gasFee[GasFeeMode.medium].maxPriorityFeePerGas, 'gwei'));
    }
  } else {
    if (!evmTx.gasPrice) {
      const gasFee = await fetchSuggestedGasFee(chainId);
      evmTx.gasPrice = bnToHex(toWei(gasFee[GasFeeMode.medium].maxFeePerGas, 'gwei'));
    }
  }

  const result = await particleAuth.ethereum.sendTransaction(evmTx);
  return result;
};

const fetchEstimateGas = async (
  param: {
    from: string;
    to?: string;
    value?: string | number;
    data?: string;
  },
  chainId: number
): Promise<string> => {
  if (!param.to || param.to.length === 0) {
    delete param.to;
  }
  const result = await particleAuth.ethereum.request({
    chainId,
    method: 'eth_estimateGas',
    params: [param],
  });
  return result;
};

const fetchSuggestedGasFee = async (chainId: number): Promise<GasFeeResult> => {
  const result = await particleAuth.ethereum.request({
    chainId,
    method: EvmEnhancedMethod.suggestedGasFees,
  });
  return result;
};

export default evmSendTransaction;
