import { AuthType } from '@particle-network/auth-core';
import React, { useMemo, useState } from 'react';
import apple from '../../../common/images/apple_icon.png';
import discord from '../../../common/images/discord_icon.png';
import email from '../../../common/images/email_icon.png';
import facebook from '../../../common/images/facebook_icon.png';
import github from '../../../common/images/github_icon.png';
import google from '../../../common/images/google_icon.png';
import jwt from '../../../common/images/jwt_icon.png';
import linkedin from '../../../common/images/linkedin_icon.png';
import microsoft from '../../../common/images/microsoft_icon.png';
import passkeys from '../../../common/images/passkeys_icon.png';
import phone from '../../../common/images/phone_icon.png';
import telegram from '../../../common/images/telegram_icon.png';
import twitch from '../../../common/images/twitch_icon.png';
import twitter from '../../../common/images/twitter_icon.png';
import SVGIcon from '../../../components/icon/svg-icon';

interface Props {
  supportAuthTypes: AuthType[];
  preferredAuthType?: AuthType;
  onPreferredAuthTypeChange?: (authType: AuthType) => void;
  thirdpartyAuthLogin: (authType: AuthType) => void;
}
const SocialLogin = (props: Props) => {
  const { supportAuthTypes, preferredAuthType, onPreferredAuthTypeChange, thirdpartyAuthLogin } = props;

  const [collapse, setCollapse] = useState(true);

  const authLogin = (authType: AuthType) => {
    thirdpartyAuthLogin(authType);
  };

  const toggleEmailOrPhone = (authType: AuthType) => {
    onPreferredAuthTypeChange?.(authType);
  };

  const imgs = {
    email,
    phone,
    facebook,
    google,
    apple,
    twitter,
    discord,
    github,
    twitch,
    microsoft,
    linkedin,
    jwt,
    telegram,
    passkeys,
  };

  const items = useMemo(() => {
    const authTypes = supportAuthTypes as AuthType[];
    const hasEmailAndPhone = authTypes.includes(AuthType.email) && authTypes.includes(AuthType.phone);
    const items: React.ReactNode[] = [];
    if (hasEmailAndPhone) {
      if (preferredAuthType === AuthType.phone) {
        items.push(<img key='email-login' src={email} alt='' onClick={() => toggleEmailOrPhone(AuthType.email)} />);
      } else {
        items.push(<img key='phone-login' src={phone} alt='' onClick={() => toggleEmailOrPhone(AuthType.phone)} />);
      }
    }
    const displayAuthTypes = authTypes.filter((authType) => authType !== AuthType.email && authType !== AuthType.phone);

    items.push(
      ...displayAuthTypes.map((authType) => (
        <img key={`${authType}-login`} src={imgs[authType]} alt='' onClick={() => authLogin(authType as AuthType)} />
      ))
    );

    const collapseIcon = (
      <div
        key='collapse-login'
        className='collapse-login-icon'
        onClick={() => setCollapse(!collapse)}
        data-is-collapse={collapse}
      >
        <SVGIcon className='collapse-icon' name='collapse_icon' />
      </div>
    );

    if (items.length > 5) {
      items.splice(4, 0, collapseIcon);
    }

    if (collapse) {
      items.splice(5, items.length - 5);
    }

    return items;
  }, [collapse, preferredAuthType, supportAuthTypes]);

  return (
    <div className='change-login-mode' data-collapse={!!collapse}>
      <div className='change-social'>{items}</div>
    </div>
  );
};

export default SocialLogin;
