import { Input } from 'antd';
import React from 'react';
import { isAutoFocusSupported } from '../../utils';
import { isNullish } from '../../utils/common-utils';
import { EyeClose, EyeOpen } from '../eyeIcon';
import styles from './index.less';

type Prop = {
  className?: string;
  maxLength?: number;
  defaultValue?: string;
  onChange?: (value: string) => void;
  onPressEnter?: (event: any) => void;
  onBlur?: (event: any) => void;
  autoFocus?: boolean;
};

const InputPassword = (prop: Prop) => {
  const inputRef = React.useRef<any>(null);

  return (
    <>
      <div className='password-input-0'>
        <Input.Password />
      </div>

      <style>{`
        ${styles as unknown as string}
        .password-input .ant-input-password {
          background-image: none !important;
        }
        .password-input .ant-input-password input {
          -webkit-text-fill-color: inherit !important;
          opacity: 1 !important;
        }
      `}</style>
      <Input.Password
        ref={inputRef}
        className={'password-input' + (prop?.className ? ` ${prop?.className}` : '')}
        iconRender={(visible) =>
          visible ? (
            <div>
              <EyeOpen />
            </div>
          ) : (
            <div>
              <EyeClose />
            </div>
          )
        }
        onChange={(e) => {
          if (prop?.onChange) {
            prop?.onChange?.(e.target.value);
          }
        }}
        defaultValue={prop?.defaultValue}
        maxLength={prop?.maxLength || 20}
        onPressEnter={(e) => prop?.onPressEnter?.(e)}
        onBlur={(e) => prop?.onBlur?.(e)}
        autoFocus={isNullish(prop?.autoFocus) && isAutoFocusSupported() ? true : prop?.autoFocus}
      />
    </>
  );
};

export default InputPassword;
