import { isConnected, isSocialAuthType } from '@particle-network/auth-core';
import { message } from 'antd';
import base64url from 'base64url';
import qs from 'qs';
import React, { useEffect, useState } from 'react';
import SocialLoading from '../../components/socialLoading';
import { useCustomNavigate, useParticleAuth } from '../../context';
import { useConnect } from '../../context/hooks';
import { isServer } from '../../utils';
import styles from './index.less';

const Index = () => {
  const { connect } = useConnect();
  const navigate = useCustomNavigate();
  const { setConnectionStatus, socialConnectCallback, setAuthCoreModal } = useParticleAuth();
  const [socialLoginLoading, setSocialLoginLoading] = React.useState(false);
  const [authType, setAuthType] = useState<string>('' as string);
  const [socialLoginFailed, setSocialLoginFailed] = useState(false);

  const onConnectCompleted = (detail: any) => {
    if (window.particle?.ethereum) {
      window.particle.ethereum.isSocialConnecting = false;
    }
    if (window.particle?.solana) {
      window.particle.solana.isSocialConnecting = false;
    }
    window.dispatchEvent(
      new CustomEvent('particle:socialConnectCompleted', {
        detail,
      })
    );
  };

  const thirdpartyLogin = async () => {
    try {
      const query = qs.parse(!isServer() ? window.location.search : '', {
        ignoreQueryPrefix: true,
      });
      const particleThirdpartyParamsContent = query?.particleThirdpartyParams as string;
      if (!particleThirdpartyParamsContent) {
        if (!isConnected()) {
          setConnectionStatus('disconnected');
        }
        return;
      }

      delete query.particleThirdpartyParams;
      const replaceUrl = (window.location.origin + window.location.pathname + '?' + qs.stringify(query)).replace(
        /\?$/,
        ''
      );
      window.history.replaceState({}, document.title, replaceUrl);
      document.title = document.title || replaceUrl;

      const particleThirdpartyParams = JSON.parse(base64url.decode(particleThirdpartyParamsContent as string));
      const { code, nonce, appState, error } = particleThirdpartyParams;
      const appStateObj = appState ? JSON.parse(base64url.decode(appState as string)) : {};
      const { authorization, chain, purpose, verifyToken } = appStateObj;

      if (error) {
        if (!isConnected()) {
          setConnectionStatus('disconnected');
        }
        if (!purpose) {
          socialConnectCallback?.onError?.(new Error(error));
        } else {
          message.error(error);
        }

        if (purpose !== 'bindLoginAccount') {
          setSocialLoginFailed(true);
        }

        return;
      }

      const authType = nonce.split('@')[0];
      setAuthType(authType);

      if (purpose === 'bindLoginAccount') {
        navigate('/login-account/bind-loading', {
          state: {
            authType,
            verifyToken,
            code,
            nonce,
          },
          replace: true,
        });
      } else {
        if (isSocialAuthType(authType)) {
          setSocialLoginLoading(true);
          setAuthCoreModal({
            particleModalVisible: true,
          });
        }
        console.log(`auth-core-modal: social:${authType} connect start`);
        await connect({ socialType: authType, code, nonce, authorization, chain });
      }
    } catch (error: any) {
      console.error('after redirect, login or bind', error);
      if (!isConnected()) {
        setConnectionStatus('disconnected');
      }
      onConnectCompleted({
        result: Object.freeze(error),
      });
    }
    setSocialLoginLoading(false);
  };

  useEffect(() => {
    thirdpartyLogin();
  }, []);

  return (
    <div className='index-container'>
      <style>{styles as unknown as string}</style>
      {socialLoginLoading && <SocialLoading authType={authType} isFailed={socialLoginFailed} />}
    </div>
  );
};

export default Index;
