import { Button, Form, Input } from 'antd';
import React, { useEffect, useRef, useState } from 'react';
import { useTranslation } from '../../../context';
import { EmailRegExp } from '../../../utils';
import styles from './login.less';

function EmailLogin(props: any) {
  const { t } = useTranslation();

  const { sendCaptcha, setShowInputCaptcha, account, supportAuthTypes, loginWithAccount } = props;

  const [form] = Form.useForm();
  const [errorTip, setErrorTip] = useState(false);
  const [noInputTip, setNoInputTip] = useState(false);

  const [loading, setLoading] = useState(false);
  const emailBoxRef = useRef<any>(null);
  const emailInput = useRef<any>(null);

  useEffect(() => {
    if (loginWithAccount && account && sendCaptcha) {
      form.setFieldsValue({
        email: account,
      });
      getCaptcha(account);
    }
  }, []);

  const getCaptcha = (email: string) => {
    emailInput.current.focus();
    if (!email) {
      setNoInputTip(true);
      setErrorTip(false);
    } else if (!EmailRegExp.test(email)) {
      setErrorTip(true);
    } else {
      sendCaptchaImpl(email);
    }
  };

  const sendCaptchaImpl = (email: string) => {
    setLoading(true);
    sendCaptcha({
      email: email,
    })
      .then((result: boolean) => {
        setShowInputCaptcha(result);
      })
      .catch(() => {
        setLoading(false);
      });
  };

  return (
    <>
      <style>{styles as unknown as string}</style>
      <div className='login-style'>
        <Form
          name='login-form'
          onFinish={(values) => getCaptcha(values.email.trim())}
          className='form-box'
          requiredMark={false}
          form={form}
          labelCol={{
            style: { textAlign: 'left' },
          }}
        >
          <div className='email-box' ref={emailBoxRef}>
            <Form.Item name='email' label='' className='form-item' initialValue={account?.includes('@') ? account : ''}>
              <Input
                className='email-input'
                placeholder={t('login.email_address') || undefined}
                ref={emailInput}
                onChange={() => {
                  setErrorTip(false);
                  setNoInputTip(false);
                }}
                readOnly={loginWithAccount}
                onFocus={() => {
                  emailBoxRef.current.setAttribute('data-focus', 'true');
                }}
                onBlur={() => {
                  emailBoxRef.current.removeAttribute('data-focus');
                }}
              />
            </Form.Item>
          </div>

          <div className='error-tip'>{errorTip && t('login.email_format_error')}</div>
          <div className='error-tip'>{noInputTip && t('login.input_email_holder')}</div>

          <Form.Item className='form-item'>
            <Button type='primary' htmlType='submit' className='primary-antd-btn submit-btn' loading={loading}>
              {t('login.get_captcha')}
            </Button>
          </Form.Item>

          {!loginWithAccount && supportAuthTypes.length > 0 && (
            <div className='login-or'>
              <div className='or-line'></div>
              {t('login.or')}
              <div className='or-line'></div>
            </div>
          )}
        </Form>
      </div>
    </>
  );
}

export default EmailLogin;
